
import { catchError } from 'rxjs/operators';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginServiceService } from '../login-service.service';
import { Location } from '@angular/common';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
@Component({
    selector: 'ndp-forget',
    templateUrl: './forget-password.component.html',
    styleUrls: ['./forget-password.component.css']
})
export class ForgetComponent implements OnInit {

    email: string;
    statusResponse: string;
    formGroup: FormGroup;
    submitted = false;
    constructor(private route: Router,
        private loginService: LoginServiceService,
        private router: Router,
        private formBuilder: FormBuilder) { }
    get getformGroupControlls() { return this.formGroup.controls; }
    ngOnInit() {

        this.formGroup = this.formBuilder.group({

            mail: ['', Validators.required]

        });

    }

    forget() {

        this.submitted = true;
        debugger;

        if (this.formGroup.invalid) {
            return;
        }
        this.loginService.forgot(this.email).pipe(catchError((err, caught) => {
            //debugger;
            console.log("ERror in forget ", err, caught);
            throw err;
        }
        )).subscribe(
            response => this.statusResponse = response.statusMessage
        );
    }

    back() {
        this.router.navigate(['login']);
    }
}