import { Injectable } from "@angular/core";
import { BaseService } from "./base-service";
import { HttpClient } from "@angular/common/http";
import { ApiResponse } from "../models/api-response";
import { Defines } from "../static/defines";
import { ApiRequest } from "../models/api-request";
import { FileTypeModel } from "../models/file-type-model";


@Injectable({
    providedIn: 'root'
})
export class LookupsService extends BaseService {
    initAuthorizationUrl() {
        this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.LOOKUPS;
        console.log("Authorization Url =>", this.authorizationUrl);
    }
    constructor(private client: HttpClient) {
        super(client);
    }

    public getfileTypes() {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.LOOKUPS
            + Defines.API_ENDPOINTS.FILE_TYPES;
        return this.http.post<ApiResponse<Array<FileTypeModel>>>
            (apiUrl, new ApiRequest(null, ""), this.httpOptions).toPromise();
    }

}
