import { Component, OnInit, Input } from '@angular/core';
import { NdpSpinnerService } from '../ndp-spinner.service';
import { AnimationService } from 'src/app/shared/services/animation.service';

@Component({
  selector: 'ndp-spinner',
  templateUrl: './ndp-spinner.component.html',
  styleUrls: ['./ndp-spinner.component.css']
})
export class NdpSpinnerComponent implements OnInit {

   /**
   * Source of the image relative to this component.ts (this is tricky and should be modified);
   */
  @Input() imgSrc: string;
  constructor(private spinnerService: NdpSpinnerService, private animationUtil: AnimationService) {
  }
  showSpinnerFlag: boolean;

  ngOnInit() {
    let scope = this;
    function changeSpinnerFlag(flag: boolean) {
      // //console.log("giSpinnerHw Flag Changed " + flag)
      scope.showSpinnerFlag = flag;
    }
    this.spinnerService.getSpinnerState().subscribe(spinnerState => changeSpinnerFlag(spinnerState)
    );
  }


}
