import { Component, OnInit } from '@angular/core';
import * as fileSaver from 'file-saver';
import { SelectItem } from 'primeng/api';
import { EntityServiceService } from 'src/app/entity/entity-service.service';
import { ReportsService } from "src/app/reports/reports.service";
import { EntityModel } from 'src/app/shared/models/entity-model';
import { ReportResponseModel } from 'src/app/shared/models/ReportResponseModel';
import { ReportRequestModel } from "src/app/shared/models/ReportRequestModel";
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';

@Component({
  selector: 'ndp-report-page',
  templateUrl: './report-page.component.html',
  styleUrls: ['./report-page.component.css']
})


export class ReportPageComponent implements OnInit {
  allEntities: Array<EntityModel>;
  entityItems: Array<SelectItem> = new Array<SelectItem>();
  selectedEntityIds: number[] = [];//TODO selectedEntityIds ... to Array of Integers 
  maxDateValue: Date = new Date();
  reportRequestModel: ReportRequestModel = new ReportRequestModel();


  constructor(private entityService: EntityServiceService, private reportsService: ReportsService
    ,private messageService: NdpMessageService) { }


  ngOnInit() {
    this.entityService.getAllEntity().subscribe(resp => {
      this.allEntities = resp.responsePayload;
      if (this.allEntities) {
        this.allEntities.forEach(entity => {
          this.entityItems.push({ label: entity.entityName, value: entity.entityId });
        })
        this.entityItems = JSON.parse(JSON.stringify(this.entityItems));
      }
    });
  }

  runReport() {
    debugger;
    if (this.reportRequestModel.entityModels.length > 0) {
      this.reportRequestModel.type = 1;

      this.reportsService.generateReport(this.reportRequestModel).subscribe(res => {
        let reportResponse: ReportResponseModel = res.responsePayload;

        let fileContent: string = decodeURIComponent(Array.prototype.map.call(atob(decodeURIComponent(reportResponse.fileContent)),
         (c) => {
          return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          debugger;
        }).join(""));/*reportResponse.fileContent*///atob(reportResponse.fileContent);
        const blob = new Blob(["\ufeff", fileContent], { type: 'text/csv' });
        fileSaver.saveAs(blob, reportResponse.filename);
        //new AngularCsv(blob, "Sample", this.csvOptions);
      });
    }else{
      this.messageService.addWarnings(['Please select at least one business entity']);
    }
  }


}

