
import { Directive, Input, Output, HostListener, ElementRef, EventEmitter } from "@angular/core";
import { AbstractControl, NG_VALIDATORS, Validator, ValidationErrors } from "@angular/forms";
import { AuthorizationService } from "../services/authorization.service";

@Directive({
	selector: '[ndpAuthorize]'
})
export class NdpAuthorizeDirective {
	@Input()
	public targetUrl: string;
	@Input()
	public targetId?: number;
	@Output('successHandler')
	public successHandler: EventEmitter<any> = new EventEmitter();


	constructor(private authService: AuthorizationService, private htmlElement: ElementRef) { }

	@HostListener('click', ['$event'])
	onclick($event) {
		// console.log("clickkkkkkkkkkkkkkkkkkkkk ", $event);
		$event.preventDefault();
		this.authService.authorizeRequest(this.targetUrl, this.targetId)
			.then(successData => {
				if (successData.status === 0) {
					console.log("Success Return ", successData);
					this.successHandler.emit();
					console.log("successHandler");
				}
			})
			.catch(err => {
				console.log("Authorization Failed");
			});
	}

}