import { Pipe, PipeTransform } from "@angular/core";
import { SmscAccountModel } from "../models/smsc-account-model";


@Pipe({
    name: 'smscNameFilter'
})
export class SmscFilterPipe implements PipeTransform {
    transform(smsc: SmscAccountModel[], filter: string): any {
        if (!smsc || !filter || filter.length < 3) {
            return smsc;
        }
        return smsc.filter(s => s.name.toLowerCase().indexOf(filter.toLowerCase()) !== -1);
    }
}