import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoginServiceService } from '../login-service.service';
import { Location } from '@angular/common';
import { ResetPasswordModel } from 'src/app/shared/models/reset-password-model';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';

@Component({
    selector: 'ndp-reset',
    templateUrl: './reset-password.component.html',
    styleUrls: ['./reset-password.component.css']
})
export class ResetComponent implements OnInit {

    model: ResetPasswordModel = new ResetPasswordModel();
    confirmPassword: string;
    formGroup: FormGroup;
    public statusResponse: string;
    submitted: boolean;

    get getformGroupControlls() { 
        console.log("controler", this.formGroup.controls);
        return this.formGroup.controls; }
    constructor(private route: Router,
        private loginService: LoginServiceService,
        private activeRoute: ActivatedRoute,
        private location: Location,
        private formBuilder: FormBuilder,
        private messageService: NdpMessageService) { }

    ngOnInit() {

        this.model.email = this.activeRoute.snapshot.paramMap.get('email');
        this.model.resetCode = this.activeRoute.snapshot.paramMap.get('code');

        this.formGroup = this.formBuilder.group({

            password: ['', [Validators.required,Validators.pattern("^(?=.*[A-Za-z])(?=.*[0-9])[A-Za-z0-9!@#$%^&*(),.?\":{}|<>\\/]{8,}$")]],
            confirmPassword: ['', Validators.required]
        });
    }

    reset() {
        this.submitted = true;
        debugger;

        if (this.formGroup.invalid) {
            return;
        }
        if (this.confirmPassword === this.model.password) {
            this.loginService.resetPassword(this.model).subscribe(
                response => this.statusResponse = response.statusMessage);
        } else {
            this.messageService.addWarnings(["Password Not Matched"]);
        }
    }

    goToLogin() {
        this.route.navigate(['login']);
    }

}