import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NdpSpinnerService {
  showSpinner = false;
  spinnerSubject: BehaviorSubject<boolean>;

  constructor() {
    this.spinnerSubject = new BehaviorSubject<boolean>(false);
  }


  getSpinnerState(): Observable<boolean> {
    // //console.log("giSpinnerHw Returning Spinner State");
    return this.spinnerSubject.asObservable();
  }

  public show() {
    // //console.log("giSpinnerHw show called");
    this.spinnerSubject.next(true);
  }

  public hide() {
    // //console.log("giSpinnerHw hide called");
    this.spinnerSubject.next(false);
  }
}
