import { Component, OnInit } from '@angular/core';
import { AppProfilesService } from '../app-profiles.service';
import { AppProfileModel } from 'src/app/shared/models/app-profile-model';
import { Router, ActivatedRoute } from '@angular/router';
import { MessageService } from 'primeng/api';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { User } from 'src/app/shared/models/user-model';
import { Defines } from 'src/app/shared/static/defines';


@Component({
  selector: 'ndp-app-profiles-list',
  templateUrl: './app-profiles-list.component.html',
  styleUrls: ['./app-profiles-list.component.css'],
  providers: [MessageService]
})
export class AppProfilesListComponent implements OnInit {

  appProfilesList: Array<AppProfileModel>;
  filteredAppProfileList: Array<AppProfileModel>;
  inialized = false;
  dropDown: boolean;
  dropDownClick: number;
  dropDownId: number;
  selectedAppProfile: AppProfileModel;
  /*=============================*/
  /* Pagination Filtering & Sorting*/
  p: number = 1;
  perPage: number = 4;
  /*FILTERING*/
  filter: string = '';
  /*SORTING*/
  key: string = 'appProfileName';
  reverse: boolean = true;

  //Authorization Vars
  authorizationUrl: string;
  defines = Defines.API_CRUD;
  //Access Levels attributes
  user: User;
  canCreate: boolean;
  canEdit: boolean;
  canView: boolean;
  canDelete: boolean;
  showConfirm: boolean;
  sort(key) {
    this.key = key;
  }
  /*=============================*/
  constructor(private appProfilesService: AppProfilesService, private router: Router,
    private ndpMessageService: NdpMessageService, private messageService: MessageService) { }

  ngOnInit() {
    this.authorizationUrl = this.appProfilesService.authorizationUrl;
    this.appProfilesService.list().subscribe(template => {
      if (template.status === 0) {
        this.appProfilesList = template.responsePayload;
        this.inialized = true;
        this.filteredAppProfileList = this.appProfilesList;
        this.setAccessLevel();
      }

    });

  }

  setAccessLevel() {
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    this.user.privilegeList.forEach(rolePrivilege => {
      switch (rolePrivilege.privilegeId) {
        case Defines.PRIVILEGES.APP_PROFILE.EDIT: {
          this.canEdit = true;
          break;
        } case Defines.PRIVILEGES.APP_PROFILE.LIST: {
          this.canView = true;
          break;
        } case Defines.PRIVILEGES.APP_PROFILE.DELETE: {
          this.canDelete = true;
          break;
        } case Defines.PRIVILEGES.APP_PROFILE.CREATE: {
          this.canCreate = true;
          break;
        }
      }
    }
    )
  }

  dropDownOpen(appProfileModle: AppProfileModel, event) {
    this.dropDownClick = 0;
    this.selectedAppProfile = appProfileModle;
    this.dropDownId = appProfileModle.appProfileId;
    this.dropDown = true;
    event.stopPropagation();
  }

  dropDownClose() {
    this.dropDownClick++;
    if (this.dropDown && this.dropDownClick > 1) {
      this.dropDown = false;
      this.dropDownId = null;
    }
  }

  clickedOut(event) {
    this.dropDownClose();
  }

  editProfile(appProfileId: number) {
    if (appProfileId != null) {
      sessionStorage.setItem('sessionAppProfileId', appProfileId + '');
    }
    this.router.navigate(['/addEditAppProfile']);
  }

  deleteProfile(appProfileId: number) {
    this.appProfilesService.delete(this.selectedAppProfile).subscribe(resp => {
      if (resp.status === 0) {
        this.appProfilesList = this.appProfilesList.filter(x => x.appProfileId !== appProfileId);
      }
    });
    this.dropDownClose();
    this.hideConfirm();
  }
  showConfirmDialog(appProfile: AppProfileModel) {
    this.showConfirm = true;
    this.selectedAppProfile = appProfile;
  }
  hideConfirm() {
    this.showConfirm = false;
  }
  // onConfirm() {
  //   this.deleteProfile();
  //   this.dropDownClose();
  //   this.messageService.clear('c');
  // }

  onReject() {
    this.messageService.clear('c');
    this.dropDownClose();
  }

  setFilteredAppProfileList(filtered: AppProfileModel[]) {
    this.filteredAppProfileList = filtered;
  }
}
