import { Directive } from "@angular/core";
import { AbstractControl, NG_VALIDATORS, Validator, ValidationErrors} from "@angular/forms";

@Directive({
	selector: "[nfSpecialCharacters]",
	providers: [{ provide: NG_VALIDATORS, useExisting: SpecialCharactersDirective, multi: true }]

})

export class SpecialCharactersDirective implements Validator {
	
	validate(control: AbstractControl): ValidationErrors | null {
		let pattern = new RegExp("^[!@#$%^&*(),.?:{}|<>]+$");
		let isAlphapetic = pattern.test(control.value);
		console.log("pattern" + isAlphapetic);
		// let isValid = !isAlphapetic;
		return !isAlphapetic ? null : {nfSpecialCharacters: true};
	  }
	}