import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';
import { EntityModel } from '../models/entity-model';

@Pipe({
    name: 'entitynameFilter'
})
export class EntityEntityNameFilter implements PipeTransform {
    transform(entities: EntityModel[], name: string): any {
        if (name == null || name.length < 3) {
            return entities;
        }
        const filtered = [];
        for (let i = 0; i < entities.length; i++) {
            const entity = entities[i];
            if (entity.entityName != null && entity.entityName.toLowerCase().includes(name.toLowerCase())) {
                filtered.push(entity);
            }
        }
        return filtered;
    }
}
