import { Component, OnInit, Input } from '@angular/core';
import { AdminViewUser } from 'src/app/shared/models/admin-view-user-model';
import { AccountService } from '../account.service';
import { Router, ActivatedRoute } from '@angular/router';
import { RoleModel } from 'src/app/shared/models/role-model';
import { EntityModel } from 'src/app/shared/models/entity-model';
import { Defines } from '../../shared/static/defines';
import { FormBuilder, FormGroup, Validators, FormControl, AbstractControl } from '@angular/forms';
import { isString } from 'util';
import { environment } from 'src/environments/environment.dev';
import { EntityServiceService } from 'src/app/entity/entity-service.service';
import { RoleManagerService } from 'src/app/roles/role-manager.service';
import { SelectItem } from 'primeng/api';

@Component({
  selector: 'ndp-add-edit-users-accounts',
  templateUrl: './add-edit-users-accounts.component.html',
  styleUrls: ['./add-edit-users-accounts.component.css']
})
export class AddEditUsersAccountsComponent implements OnInit {

  @Input() userId: number;
  tempUser: AdminViewUser;

  filter: string;

  activeDirectoryUsers;
  activeDirectoryUsersSelectItems: Array<SelectItem> = new Array<SelectItem>();
  businessEntities = new Array();
  roles = new Array();
  canAddFakeSenderFlag: boolean = false;

  //signup approval flag used for signup requests page
  signUpApproval: boolean = false;
  pending = Defines.SIGNUP_STATUS.PENDING;

  userForm: FormGroup;

  constructor(private accountService: AccountService, private entityService: EntityServiceService, private roleService: RoleManagerService, private router: Router, private route: ActivatedRoute, private formBuilder: FormBuilder) { }
  initUserForm() {
    this.userForm = this.formBuilder.group({
      userName: ['', [Validators.required]],
      password: ['', [Validators.pattern("^(?=.*[A-Za-z])(?=.*[0-9])[A-Za-z0-9!@#$%^&*(),.?\":{}|<>\\/]{8,}$")]],
      fName: ['', [Validators.required, Validators.pattern("[a-zA-Z][a-zA-Z .'-]+"), Validators.maxLength(25), Validators.minLength(2)]],
      lName: ['', [Validators.required, Validators.pattern("[a-zA-Z][a-zA-Z .'-]+"), Validators.maxLength(25), Validators.minLength(2)]],
      address: ['', [Validators.maxLength(100)]],
      email: ['', [Validators.required, Validators.email]],
      msisdn: ['', [Validators.required, this.msisdnValidator]],
      entityId: ['', [Validators.required]],
      canAddFakeSender: [false],
      needsApproval: [true, { disabled: false }],
      role: ['', [Validators.required]],
      isLdapUser: [false, { disabled: false }]
    });
  }
  ngOnInit() {

    console.log("userId:" + this.userId);
    this.initUserForm();


    //if edit mode check if edit page is requested from signup approval 
    this.signUpApproval = this.route.snapshot.routeConfig.path == "user/approve/:id";

    console.log("###############Approval Flag###################");
    console.log(this.signUpApproval);
    console.log(this.route.snapshot.routeConfig.path);

    if (this.userId == null) {
      //this.userId = this.route.snapshot.queryParams.userId;
      this.userId = +this.route.snapshot.paramMap.get('id');
      console.log("###############user id###################");
      console.log(this.userId);
    }
    if (this.userId != null && this.userId !== 0) {
      this.accountService.getUserById(this.userId).subscribe(res => {
        this.tempUser = res.responsePayload;

        if (this.signUpApproval) {
          this.initSignUpApprovalRequest();
        }

        this.userForm.setValue({
          userName: this.tempUser.userName,
          password: null,
          fName: this.tempUser.fName,
          lName: this.tempUser.lName,
          address: this.tempUser.address,
          email: this.tempUser.email,
          msisdn: this.tempUser.msisdn,
          entityId: this.tempUser.entityId,
          canAddFakeSender: this.tempUser.canAddFakeSender,
          needsApproval: this.tempUser.needsApproval,
          role: this.tempUser.role,
          isLdapUser: this.tempUser.isLdapUser
        });
        this.enableDisableNeedsApproval();
        this.enableDisableActiveDirectoryUser();
        if (this.tempUser.entityId !== null) {
          this.entityService.getEntity(this.tempUser.entityId).subscribe(res => {
            const entity = res.responsePayload;
            console.log("entity.canAddFakeSenders ", entity.canAddFakeSenders)
            this.canAddFakeSenderFlag = entity.canAddFakeSenders;
            this.tempUser.isInternal = entity.isInternal;
            this.enableDisableCanAddFakeSender(entity.canAddFakeSenders);
            setTimeout(() => { this.listRoles() });
          });
        }
      });
    } else {
      this.tempUser = new AdminViewUser();
      this.tempUser.isActive = true;
      this.tempUser.isLdapUser = false;
      this.tempUser.needsApproval = false;
      this.tempUser.canAddFakeSender = false;
    }
    this.accountService.listLdapUsers().subscribe(res => {
      this.activeDirectoryUsers = res.responsePayload;
      if (this.activeDirectoryUsers) {
        this.activeDirectoryUsers.forEach(user => {
          debugger;
          console.log(user);

          this.activeDirectoryUsersSelectItems.push({ label: user, value: user });
        })
        this.activeDirectoryUsersSelectItems = JSON.parse(JSON.stringify(this.activeDirectoryUsersSelectItems));
      }
    });
    this.entityService.getAllEntity().subscribe(res => {
      this.businessEntities = res.responsePayload;
      console.log(this.businessEntities);
    });
    this.roleService.getAllRoles().subscribe(res => {
      this.roles = res.responsePayload;
    });
  }

  get uf() {
    return this.userForm.controls;
  }

  listRoles(): Array<RoleModel> {
    let roles = Object.assign([], this.roles);
    console.log('list this.roles=>', this.roles);
    console.log('listroles=>', roles);
    if (!this.tempUser.isInternal) {

      for (let i = 0; i < roles.length; i++) {

        if (!roles[i].assignedToExtEntity || roles[i].assignedToExtEntity === null) {

          roles.splice(i, 1);
          i--;
        }
      }
    }

    return roles;
  }

  listBusinessEntities(): Array<EntityModel> {
    const entities = Object.assign([], this.businessEntities);
    if (entities) {
      console.log(this.userForm);
      if (this.userForm.get('isLdapUser').value === false) {
        for (let i = 0; i < entities.length; i++) {
          if (entities[i].isInternal) {
            entities.splice(i, 1);
            i--;
          }
        }
      }
      if (this.userId !== 0) {
        if (this.tempUser.entityId != null && (entities.find(x => x.entityId == this.tempUser.entityId) == null || entities.find(x => x.entityId == this.tempUser.entityId) == undefined)) {
          console.log(entities);
          console.log(this.businessEntities);
          const x = entities.findIndex(x => x.entityId != null);
          console.log(x);
          if (x !== undefined && x !== -1 && x !== null) {
            console.log(entities[x]);
            this.setTempUserBusinessEntity(entities[x].entityId);
          }
        }
      }
    }
    console.log('entities2', entities);
    console.log('business2', this.businessEntities);
    return entities;
  }

  enableDisableNeedsApproval(): boolean {
    let enable = true;
    if (this.tempUser.role == environment.campaignAdminId) {
      enable = false;
      this.userForm.get('needsApproval').reset({ value: false, disabled: true });
      this.tempUser.needsApproval = false;
    } else {
      let lastValue = this.userForm.get('needsApproval').value;
      this.userForm.get('needsApproval').reset({ value: lastValue, disabled: false });
    }
    return enable;
  }
  enableDisableActiveDirectoryUser() {
    if (this.signUpApproval) {
      this.userForm.get('isLdapUser').reset({ value: false, disabled: false });
    } else {
      this.userForm.get('isLdapUser').reset({ value: this.tempUser.isLdapUser, disabled: false });
    }
  }
  enableDisableCanAddFakeSender(canAddFakeSender: boolean) {
    if (canAddFakeSender) {
      let lastValue = this.userForm.get('canAddFakeSender').value;
      this.userForm.get('canAddFakeSender').reset({ value: lastValue, disabled: false });
    } else {
      this.userForm.get('canAddFakeSender').reset({ value: false, disabled: true });
    }
  }
  createUser() {
    this.accountService.scrollIfFormHasErrors(this.userForm);

    this.assignFormToUser();
    if (!this.userForm.invalid) {
      this.accountService.createUser(this.tempUser).subscribe(res => {
        if (res.responsePayload > 0) {
          this.router.navigate(["user/list"]);
        }
        console.log(res.responsePayload);
      });
    } else {
      this.accountService.markFormGroupTouched(this.userForm);
    }
  }
  updateUser() {
    this.accountService.scrollIfFormHasErrors(this.userForm);
    this.assignFormToUser();
    if (!this.userForm.invalid) {
      if (this.signUpApproval) {
        this.approveUser();
      } else {
        this.accountService.updateUser(this.tempUser).subscribe(res => {
          if (res.responsePayload > 0) {
            this.router.navigate(["user/list"]);
          }
        });
      }
    } else {
      this.accountService.markFormGroupTouched(this.userForm);
    }
  }

  cancel() {
    if (this.signUpApproval) {
      this.router.navigate(["requests"]);
    } else {
      this.router.navigate(["user/list"]);
    }
  }

  assignFormToUser() {
    this.tempUser.userName = this.userForm.get('userName').value;
    console.log("username:", this.tempUser.userName);
    this.tempUser.password = this.userForm.get('password').value;
    this.tempUser.entityId = this.userForm.get('entityId').value;
    this.tempUser.fName = this.userForm.get('fName').value;
    this.tempUser.lName = this.userForm.get('lName').value;
    this.tempUser.address = this.userForm.get('address').value;
    this.tempUser.email = this.userForm.get('email').value;
    this.tempUser.msisdn = this.userForm.get('msisdn').value;
    this.tempUser.canAddFakeSender = this.userForm.get('canAddFakeSender').value;
    this.tempUser.needsApproval = this.userForm.get('needsApproval').value;
    this.tempUser.role = this.userForm.get('role').value;
    this.tempUser.isLdapUser = this.userForm.get('isLdapUser').value;
  }

  addUser() {
    this.router.navigate(["user/add"]);
  }

  isEditMode() {
    return this.tempUser.id != null;
  }

  setTempUserRole(id: number) {
    this.tempUser.role = id;
    this.userForm.get("role").setValue(id);
    this.tempUser.roleLabel = this.roles.find(role => {
      return role.id == id;
    }).label;
    this.enableDisableNeedsApproval();
    console.log(this.tempUser.roleLabel);
  }

  setTempUserBusinessEntity(id: number) {
    console.log("entity ID :" + id);
    let entity: EntityModel;
    this.tempUser.entityId = id;
    this.userForm.get("entityId").setValue(id);
    entity = this.businessEntities.find(entity => {
      return entity.entityId == id;
    });
    this.tempUser.entityName = entity.entityName;
    this.tempUser.isInternal = entity.isInternal;
    this.enableDisableCanAddFakeSender(entity.canAddFakeSenders);
    this.setTempUserRole(this.listRoles().find(x => x.id != null).id);
  }

  clearUserNameControl() {
    if (this.userId === 0) {
      this.userForm.get('userName').reset('');
    }
  }

  /**start handle signup approval page */

  initSignUpApprovalRequest() {
    //  this.tempUser.isLdapUser = false;
    this.tempUser.isActive = true;
  }

  approveUser() {
    this.setApprovalStatus();

    this.accountService.approveUser(this.tempUser).subscribe(res => {
      if (res.errors.length > 0 || res.status != 0) {
        this.tempUser.signupStatus = Defines.SIGNUP_STATUS.PENDING;
      } else {
        this.router.navigate(["requests"]);
      }
    });
  }

  setApprovalStatus() {
    this.tempUser.signupStatus = Defines.SIGNUP_STATUS.APPROVED;
  }
  /**end handle signup approval page */

  msisdnValidator(control: AbstractControl): { [key: string]: boolean } | null {
    if (control.value == '') {
      control.setValue(null, { emitEvent: false });
    }
    if (control.value !== undefined && control.value !== null && (isNaN(control.value) || !control.value.match('[+]?[0][1][1|0|2|5][0-9]{8}'))) {
      return { 'notValid': true };
    }
    return null;
  }
  passwordValidator(control: AbstractControl): { [key: string]: boolean } | null {
    if (control.value == '') {
      control.setValue(null, { emitEvent: false });
    }
    if (control.value !== undefined && control.value !== null && control.value.length < 5) {
      return { 'tooShort': true };
    }
    return null;
  }
}


