import { Component, OnInit, OnDestroy } from '@angular/core';
import { CampaignService } from '../campaign.service';
import { CampaignModel } from 'src/app/shared/models/campaign-model';
import { Dropdown } from 'primeng/dropdown';
import { Defines } from 'src/app/shared/static/defines';
import { Router } from '@angular/router';
import { defineBase } from '@angular/core/src/render3';
import { User } from 'src/app/shared/models/user-model';
import { CampaignRequestModel } from 'src/app/shared/models/campaign-request-model';
import { Observable, of, timer } from 'rxjs';

@Component({
  selector: 'ndp-campaign-list',
  templateUrl: './campaign-list.component.html',
  styleUrls: ['./campaign-list.component.css']
})
export class CampaignListComponent implements OnInit, OnDestroy {

  initialized: boolean;
  campaignsList: Array<CampaignModel>;
  filteredCampaignsList: Array<CampaignModel>;
  dropDown: boolean;
  dropDownClick: number;
  dropDownId: number;
  priorities = ['High', 'Medium', 'Low'];
  detailedCampaign: CampaignModel;
  detailedCampaignId: number;
  entitiesMap: Map<String, String>;
  /*=============================*/
  /* Pagination Filtering & Sorting*/
  p = 1;
  perPage = 10;
  /*FILTERING*/
  filter: string;
  /*SORTING*/
  key = 'startDate';
  reverse = true;
  /*=============================*/
  deleteConfirm = false;
  deleteConfirmId: number;

  status = Defines.CAMPAIGN_DISPLAY_STATUS.ALL;

  //Authorization Vars
  authorizationUrl: string;
  defines = Defines.API_CRUD;
  // Access Levels attributes
  user: User;
  listAccessLevel = 0;
  editAccessLevel = 0;
  deleteAccessLevel = 0;
  canCreate: boolean;
  canApproveflag = false;

  contextMenu = false;
  justOpened = false;

  progress = 30;
  intervalId: any;
  dateFilter: boolean;
  dateFilterStart: Date;
  dateFilterEnd: Date;
  entityFilter: String;

  setAccessLevel() {
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    if (this.user.privilegeList) {
      this.user.privilegeList.forEach(rolePrivilege => {
        switch (rolePrivilege.privilegeId) {
          case Defines.PRIVILEGES.CAMPAIGN.EDIT: {
            this.editAccessLevel = rolePrivilege.accessLevel;
            break;
          } case Defines.PRIVILEGES.CAMPAIGN.LIST: {
            this.listAccessLevel = rolePrivilege.accessLevel;
            break;
          } case Defines.PRIVILEGES.CAMPAIGN.DELETE: {
            this.deleteAccessLevel = rolePrivilege.accessLevel;
            break;
          } case Defines.PRIVILEGES.CAMPAIGN.CREATE: {
            this.canCreate = true;
            break;
          } case Defines.PRIVILEGES.CAMPAIGN.approve: {
            this.canApproveflag = true;
            break;
          }
        }
      }
      );
    }
  }

  setFilterStatus(s: number) {
    this.status = s;
  }

  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }

  constructor(private campaignService: CampaignService, private router: Router) { }

  ngOnInit() {
    this.initialized = false;
    this.campaignsList = new Array<CampaignModel>();
    this.refreshData();
    this.intervalId = setInterval(() => { this.refreshData(); }, 30000);
    this.filteredCampaignsList = this.campaignsList;
    this.authorizationUrl = this.campaignService.authorizationUrl;
  }

  getAllEntities() {
    if (this.campaignsList) {
      console.log('list size', this.campaignsList.length);
      this.entitiesMap = new Map();
      this.campaignsList.forEach(camp => {
        this.entitiesMap.set(camp.entityName, camp.entityName);
      });
      console.log('entities', this.entitiesMap.keys());
    }
  }

  entitiesList() {
    return Array.from(this.entitiesMap.keys());
  }
  canApprove() {
    return !this.user.needsApproval;
  }

  ngOnDestroy() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  }

  async refreshData() {
    this.campaignService.list().subscribe(templates => {
      this.campaignsList = templates.responsePayload;
      this.initialized = true;
      this.getAllEntities();
      this.setAccessLevel();
      this.setAccessLevels(this.campaignsList);
    });
  }

  private setAccessLevels(campList: Array<CampaignModel>) {
    if (campList) {
      campList.forEach(model => {
        if (model.createdBy === this.user.id) {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            model.canEdit = true;
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            model.canDelete = true;
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            model.canView = true;
          }
        } else if (model.entityId === this.user.entityId) {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            model.canEdit = true;
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            model.canDelete = true;
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            model.canView = true;
          }
        } else {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            model.canEdit = true;
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            model.canDelete = true;
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            model.canView = true;
          }
        }
      });
    }
  }

  resetPagination() {
    if (this.p > 1) {
      this.p = 1;
    }
  }
  showDropDown(event: Event, campaign: number) {
    this.dropDown = true;
    this.dropDownId = campaign;
    event.stopPropagation();
  }
  hideDropDown() {
    this.dropDown = false;
    this.dropDownId = null;
    event.stopPropagation();
  }

  showDetails(event: Event, campaign: CampaignModel) {
    debugger;
    if (this.dropDown !== false) {
      this.dropDown = false;
    }
    if (this.detailedCampaignId !== campaign.campaignId) {
      this.detailedCampaign = campaign;
      this.detailedCampaignId = campaign.campaignId;
    } else {
      this.hideDetails();
    }
    event.stopPropagation();
  }
  hideDetails() {
    this.detailedCampaign = null;
    this.detailedCampaignId = null;
    event.stopPropagation();
  }

  editButtonClick(campaignId: number) {
    this.router.navigate(['campaigns/edit', campaignId]);
  }

  cloneButtonClick(campaignId: number) {
    this.router.navigate(['campaigns/clone', campaignId]);
  }

  confirmDelete(id: number) {
    this.deleteConfirm = true;
    this.deleteConfirmId = id;
  }
  hideConfirmDelete() {
    this.deleteConfirm = false;
    this.deleteConfirmId = null;
  }

  delete(campaign: CampaignModel) {
    const campaignRequest = new CampaignRequestModel();
    campaignRequest.campaignModel = campaign;
    this.deleteConfirm = false;
    this.campaignService.delete(campaignRequest).subscribe(template => {
      if (template.errors.length === 0) {
        this.campaignsList = this.campaignsList.filter(x => x.campaignId !== campaign.campaignId);
        this.status = Defines.CAMPAIGN_DISPLAY_STATUS.ALL;
      }
    });
  }

  getDuration(campaign: CampaignModel) {
    if (campaign.status === Defines.CAMPAIGN_STATUS.PENDING) {
      return 'Pending';
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.PAUSED) {
      return 'Paused';
    }
    const date1 = new Date();
    // const date2 = new Date(campaign.endDate);
    const date2 = new Date(campaign.startDate);
    if (date1 < date2) {
      const diffInMs: number = date2.getTime() - +date1.getTime();
      const diffInDays: number = diffInMs / 1000 / 60 / 60 / 24;
      const diffInHours: number = (diffInDays - Math.trunc(diffInDays)) * 24;

      const remainingMinutes: number = (diffInHours - Math.trunc(diffInHours)) * 60;
      return Math.trunc(diffInDays) + ' : ' + Math.trunc(diffInHours) + ' : ' + Math.round(remainingMinutes);
    }
  }

  getStatus(campaign: CampaignModel) {
    const startDate = new Date(campaign.startDate);
    const endDate = new Date(campaign.endDate);
    const currentDate = new Date();

    if (campaign.status === Defines.CAMPAIGN_STATUS.PAUSED) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.PAUSED;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.PENDING) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.PENDING;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.PROCESSING) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.PROCESSING_FILE;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.READY && currentDate < startDate) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.SCHEDULED;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.FAILED || campaign.status === Defines.CAMPAIGN_STATUS.SNAPSHOT_FAILED || campaign.status === Defines.CAMPAIGN_STATUS.ALL_MESSAGES_FAILED) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.FAILED;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.HANDLING || campaign.status === Defines.CAMPAIGN_STATUS.SMS_ENQUEUED
      || (campaign.status === Defines.CAMPAIGN_STATUS.READY && currentDate >= startDate)) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.RUNNING;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.SUCCESS) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.FINISHED;
    }
  }
  get displayStatus() {
    return Defines.CAMPAIGN_DISPLAY_STATUS;
  }

  toggleCampaignPause(campaign: CampaignModel) {
    const s = this.getStatus(campaign);
    const campaignRequest = new CampaignRequestModel();
    campaignRequest.campaignModel = campaign;
    if (s === Defines.CAMPAIGN_DISPLAY_STATUS.RUNNING || s === Defines.CAMPAIGN_DISPLAY_STATUS.SCHEDULED) {
      campaignRequest.campaignModel.status = Defines.CAMPAIGN_STATUS.PAUSED;
      this.campaignService.pause(campaignRequest).subscribe(template => {
        console.log('Pausing  ==> ', template.responsePayload);
        if (template.status === 200) {
          campaign.status = Defines.CAMPAIGN_STATUS.PAUSED;
        }
      });
    } else if (s === Defines.CAMPAIGN_DISPLAY_STATUS.PAUSED) {
      campaignRequest.campaignModel.status = Defines.CAMPAIGN_STATUS.READY;
      this.campaignService.pause(campaignRequest).subscribe(template => {
        console.log('Unpausing  ==> ', template.responsePayload);
        if (template.status === 200) {
          campaign.status = Defines.CAMPAIGN_STATUS.READY;
        }
      });
    }
  }

  approveCampaign(campaign: CampaignModel) {
    campaign.status = Defines.CAMPAIGN_STATUS.APPROVED;
    const campaignRequest = new CampaignRequestModel();
    campaignRequest.campaignModel = campaign;
    this.campaignService.approve(campaignRequest).subscribe(template => {
      console.log('Approve Campaign ', template.responsePayload);
    });
  }

  setFilteredCampaignsList(filtered: CampaignModel[]) {
    this.filteredCampaignsList = filtered;
  }

  toggleDateFilter() {
    if (this.dateFilter) {
      this.dateFilter = !this.dateFilter;
    } else {
      this.dateFilter = true;
    }
  }
  closeDateFilter(e: Event) {
    if (this.dateFilter === true) {
      this.dateFilter = false;
      e.stopPropagation();
    }
  }

  adjustDate(d: Date, type: boolean) {
    if (d) {
      if (!type) {
        d.setHours(0);
        d.setMinutes(0);
        d.setSeconds(0);
      } else {
        d.setHours(23);
        d.setMinutes(59);
        d.setSeconds(59);
      }
    }
  }

  getStatusText(campaign: CampaignModel) {
    switch (this.getStatus(campaign)) {
      case Defines.CAMPAIGN_DISPLAY_STATUS.PAUSED:
        return 'Paused';
      case Defines.CAMPAIGN_DISPLAY_STATUS.PENDING:
        return 'Pending';
      case Defines.CAMPAIGN_DISPLAY_STATUS.PROCESSING_FILE:
        return 'Processing File';
      case Defines.CAMPAIGN_DISPLAY_STATUS.SCHEDULED:
        return 'Scheduled';
      case Defines.CAMPAIGN_DISPLAY_STATUS.RUNNING:
        return 'Running';
      case Defines.CAMPAIGN_DISPLAY_STATUS.FINISHED:
        return 'Finished';
      case Defines.CAMPAIGN_DISPLAY_STATUS.FAILED:
        return 'Failed';
    }
  }
}
