import { Directive } from "@angular/core";
import { AbstractControl, NG_VALIDATORS, Validator, ValidationErrors} from "@angular/forms";

/**
 * This validator works like "required" but it does not allow whitespace either
 *
 * @export
 * @class RequiredDirective
 * @implements {Validator}
 */
@Directive({
	selector: "[ndpRequired]",
	providers: [{ provide: NG_VALIDATORS, useExisting: RequiredDirective, multi: true }]
})
export class RequiredDirective implements Validator {

	validate(control: AbstractControl): ValidationErrors | null {
		// let isWhitespace = (control.value || '').trim().length === 0;
		// let isValid = !isWhitespace;
		// return isValid ? null : {required: true};
		return null;
	  }

}