import { Pipe, PipeTransform } from '@angular/core';
import { RoleModel } from '../models/role-model';

@Pipe({
    name: 'roleLabelFilter'
})
export class RoleLabelFilter implements PipeTransform {
    transform(roles: RoleModel[], label: string): any {
        if (roles == null || label == null || label.length < 3) {
            return roles;
        }
        const filtered = [];
        for (let i = 0; i < roles.length; i++) {
            const role = roles[i];
            if (role.label != null && role.label.toLowerCase().includes(label.toLowerCase())) {
                filtered.push(role);
            }
        }
        return filtered;
    }
}
