import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RoleModel } from 'src/app/shared/models/role-model';
import { PrivilegeModel } from 'src/app/shared/models/privilege-model';
import { RolePrivilegeModel } from 'src/app/shared/models/role-privilege-model';
import { LookupModel } from 'src/app/shared/models/lookup-model';
import { RoleManagerService } from '../role-manager.service';
import { SelectItem } from 'primeng/api';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';

@Component({
  selector: 'ndp-add-edit',
  templateUrl: './add-edit.component.html',
  styleUrls: ['./add-edit.component.css']
})
export class AddEditComponent implements OnInit {

  @Input("rolemodel")
  inputRole: RoleModel;

  editedRole: RoleModel;

  @Input("privilegeList")
  allPrivileges = new Array<PrivilegeModel>();

  @Input("mode")
  mode: number;

  @Input("accessLevelList")
  accessLevelList = new Array<LookupModel>();

  @Output("outputrole")
  outputrole = new EventEmitter<RoleModel>();

  @Output("isCanceled")
  isCanceled = new EventEmitter<number>();

  privIdx: number;
  levelstr: string;
  selectedPrivileges: number[] = [];
  levelselectItem: SelectItem[];
  rolePrivModelMap: any = {};

  constructor(private roleServicer: RoleManagerService, private messageService: NdpMessageService) { }

  ngOnInit() {


    if (this.mode == 0) {
      this.editedRole = new RoleModel();

    } else {
      this.editedRole = Object.assign({}, this.inputRole);
      console.log("On Init");
      this.editedRole.privilegeList.forEach(rolePriv => {
        this.selectedPrivileges.push(rolePriv.privilegeId);
        console.log("adding Role Priv =>", rolePriv);
        this.rolePrivModelMap[rolePriv.privilegeId] = rolePriv;
      });
      //debugger;
      this.levelselectItem = [];
      this.levelselectItem.push({ label: "Select level", value: 0 })
      this.accessLevelList.forEach(level => {

        this.levelselectItem.push({ label: level.label, value: level.id })
      });
      console.log("RolePrivModel Map =>", this.rolePrivModelMap);
    }
    //debugger;

  }
  prepareRolePrivilegeModelMap() {

  }
  editrow() {
    debugger
    console.log("editedRole ==>> ", this.editedRole);
  }

  onSelectionChange(checked: any, privelegeMod: PrivilegeModel) {
    console.log("Event", event);
    if (checked) {
      this.rolePrivModelMap[privelegeMod.id] = new RolePrivilegeModel(this.editedRole.id, privelegeMod.id, null);
    } else {
      this.rolePrivModelMap[privelegeMod.id] = null;
    }

    console.log("selected priv", this.rolePrivModelMap);
  }
  getRolePrivilegeModel = function (privilegeId: number): RolePrivilegeModel {
    console.log("getPrivilegeModel");
    let rolePriv: RolePrivilegeModel;
    rolePriv = this.editedRole.privilegeList.find(rp => rp.privilegeId == privilegeId);
    console.log("Found Role Priv => ", rolePriv);
    return rolePriv;
  }

  isparent(privilegeId: number): boolean {
    let parent = false;
    this.allPrivileges.forEach(privi => {
      if (privi.id == privilegeId) {
        parent = true;

      }
    }
    );

    return parent;
  }

  saveRole() {
    this.editedRole.privilegeList = [];
    let isvalid = true;
    this.selectedPrivileges.forEach(rolePriv => {
      //debugger;
      if (this.isparent(rolePriv)) {
        this.editedRole.privilegeList.push(new RolePrivilegeModel(this.editedRole.id,
          this.rolePrivModelMap[rolePriv].privilegeId, null))
      } else {
        if (this.rolePrivModelMap[rolePriv].level == null || this.rolePrivModelMap[rolePriv].level == 0) {
          this.messageService.addWarnings(["Please select privilege level "]);
          isvalid = false;
        } else {
          this.editedRole.privilegeList.push(new RolePrivilegeModel(this.editedRole.id,
            this.rolePrivModelMap[rolePriv].privilegeId, this.rolePrivModelMap[rolePriv].level))
        }
      }
      console.log("adding Role Priv =>", rolePriv);
    });

    //debugger;
    if (this.editedRole.label == null || this.editedRole.label == "") {
      this.messageService.addWarnings(["Please enter role label "]);
      isvalid = false;
    }

    if (this.mode == 1 && isvalid) {
      //debugger;
      console.log("edited role ", this.editedRole);
      this.roleServicer.editRole(this.editedRole).subscribe(resp => {
        //debugger;
        console.log("set output params ")
        this.outputrole.emit(this.editedRole);
      });
    } else if (this.mode == 0 && isvalid) {
      //debugger;
      this.roleServicer.addRole(this.editedRole).subscribe(resp => {
        //debugger;
        console.log("inserted Id", resp.responsePayload)
        this.editedRole.id = resp.responsePayload;
        this.outputrole.emit(this.editedRole);

      });


    }

  }

  Cancel() {
    this.isCanceled.emit(1);
  }


  changeLevel(event: any, privilegeId: number) {
    //debugger;
    this.rolePrivModelMap[privilegeId].level = event.value;
    console.log("test : " + event);
  }
}
