import { Component, OnInit } from '@angular/core';
import { EntityServiceService } from '../entity-service.service';
import { EntityModel } from 'src/app/shared/models/entity-model';
import { forEach } from '@angular/router/src/utils/collection';
import { Router } from '@angular/router';
import { User } from 'src/app/shared/models/user-model';
import { Defines } from 'src/app/shared/static/defines';

@Component({
  selector: 'ndp-entity-list',
  templateUrl: './entity-list.component.html',
  styleUrls: ['./entity-list.component.css']
})
export class EntityListComponent implements OnInit {



  allEntities: Array<EntityModel>;
  filteredEntities = null;
  inialized = false;
  showDetailsFlag = false;
  ShowDetailsSelectedId = 0;
  dropDown: boolean;
  dropDownEntityId: number;
  showEditFlag = false;
  showEditId = 0;
  deleteConfirm = false;
  deletedId: number;
  entitytoView: EntityModel;
  user: User;
  listAccessLevel: number = 0;
  editAccessLevel: number = 0;
  deleteAccessLevel: number = 0;
  canCreate: boolean;
  constructor(private entityService: EntityServiceService, private route: Router) { }

  /*=============================*/
  /* Pagination Filtering & Sorting*/
  p = 1;
  perPage = 5;
  /*FILTERING*/
  filter: string;
  /*SORTING*/
  key = 'entityId';
  reverse = true;
  /*=============================*/

  //Filter Menu
  filterMenuEnabled: boolean;

  //filtering
  statusFilter: boolean;
  typeFilter: string;
  nameFilter: string;

  //Authorization Vars
  authorizationUrl: string;
  defines = Defines.API_CRUD;

  ngOnInit() {
    this.authorizationUrl = this.entityService.authorizationUrl;
    this.statusFilter = null;
    this.typeFilter = 'all';
    this.allEntities = new Array<EntityModel>();
    //this.filteredEntities = this.allEntities;
    this.entityService.mode = 0;
    this.entityService.modeltoEdit = null;
    this.setAccessLevel();
    console.log("Can create", this.canCreate)
    this.listEntity();
  }

  listEntity() {
    this.entityService.getAllEntity().subscribe(resp => {
      this.allEntities = resp.responsePayload;
      this.inialized = true;
      this.allEntities.forEach(entity => {
        if (entity.createdBy === this.user.id) {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            entity.canEdit = true
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            entity.canDelete = true
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            entity.canView = true
          }
        } else if (entity.creatorEntityId === this.user.entityId) {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            entity.canEdit = true
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            entity.canDelete = true
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            entity.canView = true
          }
        } else {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            entity.canEdit = true
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            entity.canDelete = true
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            entity.canView = true
          }
        }
      });
    });
  }

  addEntity() {
    this.route.navigateByUrl('/entity/add');
  }
  showDetails(showdetialsId: number) {
    this.showDetailsFlag = true;
    this.ShowDetailsSelectedId = showdetialsId;
  }
  showHideFilterMenu() {
    this.filterMenuEnabled = !this.filterMenuEnabled;
  }
  setStatusFilter(statusToFilterBy: string) {
    switch (statusToFilterBy) {
      case "all":
        this.statusFilter = null;
        break;
      case "active":
        this.statusFilter = true;
        break;
      case "inactive":
        this.statusFilter = false;
        break;
    }
  }
  setTypeFilter(statusToFilterBy: string) {
    switch (statusToFilterBy) {
      case "all":
        this.typeFilter = 'all';
        break;
      case "internal":
        this.typeFilter = 'internal';
        break;
      case "external":
        this.typeFilter = 'external';
        break;
    }
  }
  dropDownOpen(event: Event, entity: EntityModel) {
    event.stopPropagation();
    if (!this.dropDown || this.dropDownEntityId != entity.entityId) {
      this.dropDownEntityId = entity.entityId;
      this.dropDown = true;
    } else {
      this.dropDown = false;
    }
  }

  dropDownClose() {
    this.dropDownEntityId = null;
    this.dropDown = false;
  }


  editEntity(entity: EntityModel) {
    this.dropDownClose();
    // this.entitytoEdit = entity;
    // this.showEditFlag = true;
    // this.showEditId =  entity.entityId;
    // this.entityService.cacheSessionObject(Defines.SESSION_KEYS.entityToEdit, JSON.stringify(entity));
    this.entityService.setmodelTobeEdit(entity);
    this.route.navigate(['/entity/add']);
  }

  afterEditentity(outputEntity: any) {

    if (outputEntity != null) {
      console.log("output entity ", outputEntity);

      let tempEntities = [];
      this.allEntities.forEach(entity => {
        if (entity.entityId == outputEntity.entityId) {
          console.log("list before ", this.allEntities)
          tempEntities.push(outputEntity);
          console.log("list after ", this.allEntities)
        }
        else {
          tempEntities.push(entity);
        }
      });

      console.log("list before ", this.allEntities)

      this.allEntities = tempEntities;
      console.log("list after ", this.allEntities)
    }
    this.showEditFlag = false;
    this.showEditId = null;
  }


  deleteEntity(inEntity: EntityModel) {
    this.dropDownClose();
    this.entityService.deleteEntity(inEntity.entityId).subscribe(resp => {
      if (resp.status === 0) {
        this.allEntities = this.allEntities.filter(x => x.entityId !== inEntity.entityId);
      } else {
        this.hideConfirmDelete()
      }
    });
  }


  viewEntity(entity: EntityModel) {
    this.dropDownClose();
    this.entitytoView = entity;
    this.showEditFlag = true;
    this.showEditId = entity.entityId;
  }
  doAfterViewAction($event) {
    console.log("Action ", $event);
    if ($event == 1)
      this.editEntity(this.entitytoView);
    else if ($event == 2)
      this.showEditFlag = false;
  }

  showConfirmDelete(entity: EntityModel) {
    this.deleteConfirm = true;
    this.deletedId = entity.entityId;
  }

  hideConfirmDelete() {
    this.deleteConfirm = false;
    this.deletedId = 0;
  }

  setFilteredEntities(filtered: EntityModel[]) {
    this.filteredEntities = filtered;
  }

  setAccessLevel() {
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    this.user.privilegeList.forEach(rolePrivilege => {
      switch (rolePrivilege.privilegeId) {
        case Defines.PRIVILEGES.ENTITY.EDIT: {
          this.editAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.ENTITY.LIST: {
          this.listAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.ENTITY.DELETE: {
          this.deleteAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.ENTITY.CREATE: {
          this.canCreate = true;
          break;
        }
      }
    }
    )
  }

}
