import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AnimationService {
  public fadeOutElement(elementName: string, animationTime: number) {
    if (!animationTime) {
      animationTime = 2000;
    }
    var hwhwTime = animationTime;
    let elementToAnimate: any;
    elementToAnimate = document.getElementById(elementName);
    //console.log("Animating Div " + elementToAnimate + ", Duration:" + animationTime);
    // keyframes
    elementToAnimate.animate({ opacity: ['1', '0'] },
      { duration: animationTime + 1, iterations: 1 });
  }

  public fadeInElement(elementName: string, animationTime: number) {
    if (!animationTime) {
      animationTime = 2000;
    }
    var elementToAnimate: any = document.getElementById(elementName);
    //console.log(document.getElementById(elementName));
    // keyframes
    elementToAnimate.animate({ opacity: ['0', '1'] },
      { duration: animationTime, iterations: 1 });
  }

  public rotateElementInfinitely(divName: string, fullRotationTime: number) {
    if (!fullRotationTime) {
      fullRotationTime = 2000;
    }
    var divToRotate: any = document.getElementById(divName);
    //console.log(document.getElementById(divName));
    // keyframes
    divToRotate.animate({ transform: ['rotate(0deg)', 'rotate(360deg)'] },
      { duration: fullRotationTime, iterations: Infinity });
  }
}
