import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';

@Pipe({
    name: 'userStatusFilter'
})
export class UsersStatusFilter implements PipeTransform {
    transform(users: AdminViewUser[], status: boolean): any {
        if (status == null) {
            return users;
        }
        const filtered = [];
        for (let i = 0; i < users.length; i++) {
            const user = users[i];
            if (user.isActive == status) {
                filtered.push(user);
            }
        }
        return filtered;
    }
}
