import { Directive, Input, OnInit, ElementRef, HostListener, AfterViewInit, AfterViewChecked } from '@angular/core';

@Directive({
  selector: '[ndpButton]'
})
export class NdpButtonDirective implements OnInit, AfterViewChecked {

  @Input() public buttonClass: string[];
  constructor(private htmlElement: ElementRef) { }
  ngOnInit(): void {
    // console.log("hwhwhw");
    // console.log("This Html Element =>", this.htmlElement);
    // let classList: DOMTokenList = this.htmlElement.nativeElement.classList;

    // console.log("Element ClassList => ", classList);

    // classList.forEach((value: string, key: number, parent: DOMTokenList) => {
    //   console.log("Removing " + value);
    //   // this.htmlElement.nativeElement.classList.remove(value)
    // });
    // this.htmlElement.nativeElement.classList.add(this.buttonClass);
  }
  ngAfterViewChecked() {
    console.log("hwhwhw");
    console.log("This Html Element =>", this.htmlElement);
    let classList: DOMTokenList = this.htmlElement.nativeElement.classList;

    console.log("Element ClassList => ", classList);

    classList.forEach((value: string, key: number, parent: DOMTokenList) => {
      console.log("Removing " + value);
      this.htmlElement.nativeElement.classList.remove(value)
    });
    this.buttonClass.forEach(className => {
      this.htmlElement.nativeElement.classList.add(className);
    })

    classList = this.htmlElement.nativeElement.classList;
    console.log(classList);
  }

}
