import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';

@Pipe({
    name: 'usernameFilter'
})
export class UsersUsernameFilter implements PipeTransform {
    transform(users: AdminViewUser[], username: string): any {
        if (users == null || username == null || username.length < 3) {
            return users;
        }
        const filtered = [];
        for (let i = 0; i < users.length; i++) {
            const user = users[i];
            if (user.userName != null && user.userName.toLowerCase().includes(username.toLowerCase())) {
                filtered.push(user);
            }
        }
        return filtered;
    }
}
