import { Injectable } from '@angular/core';
import { BaseService } from '../shared/services/base-service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { Defines } from '../shared/static/defines';
import { ApiRequest } from '../shared/models/api-request';
import { SmsTemplateModel } from '../shared/models/sms-template-model';

@Injectable({
  providedIn: 'root'
})
export class SmsTemplatesService extends BaseService {
  constructor(private client: HttpClient) {
    super(client);
  }
  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.SMSTEMPLATES;
    console.log("Authorization Url =>", this.authorizationUrl);
}
  public listSmsTemplates(): Observable<ApiResponse<Array<SmsTemplateModel>>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.SMSTEMPLATES + Defines.API_ENDPOINTS.LIST;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<SmsTemplateModel>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);
    return obs;
  }

  public getSmsTemplate(smsTemplate: SmsTemplateModel): Observable<ApiResponse<Array<SmsTemplateModel>>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.SMSTEMPLATES + Defines.API_ENDPOINTS.GET;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<SmsTemplateModel>>>
      (apiUrl, new ApiRequest(smsTemplate, ''), this.httpOptions);
    return obs;
  }

  public updateSmsTemplate(smsTemplate: SmsTemplateModel): Observable<ApiResponse<any>> {
    console.log('Service Create Template ==> ', smsTemplate);
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.SMSTEMPLATES + Defines.API_ENDPOINTS.UPDATE;
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(smsTemplate, ''), this.httpOptions);
    return obs;
  }

  public createSmsTemplate(smsTemplate: SmsTemplateModel): Observable<ApiResponse<any>> {
    console.log('Service Create Template ==> ', smsTemplate);
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.SMSTEMPLATES + Defines.API_ENDPOINTS.CREATE;
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(smsTemplate, ''), this.httpOptions);
    return obs;
  }

  public deleteSmsTemplate(smsTemplate: SmsTemplateModel): Observable<ApiResponse<any>> {
    console.log('Service Delete Template ==> ', smsTemplate);
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.SMSTEMPLATES + Defines.API_ENDPOINTS.DELETE;
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(smsTemplate, ''), this.httpOptions);
    return obs;
  }
}