import { Component, OnInit, Input } from '@angular/core';
import { AppProfileModel } from 'src/app/shared/models/app-profile-model';
import { Router, ActivatedRoute } from '@angular/router';
import { AppProfilesService } from '../app-profiles.service';
import { FormGroup, FormControl, FormBuilder, Validators, ValidatorFn, AbstractControl, ValidationErrors } from '@angular/forms';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';


@Component({
  selector: 'ndp-app-profiles-add-edit',
  templateUrl: './app-profiles-add-edit.component.html',
  styleUrls: ['./app-profiles-add-edit.component.css']
})
export class AppProfilesAddEditComponent implements OnInit {
  @Input() appProfileID: number;
  mainForm: FormGroup;
  appProfile: AppProfileModel;
  submitted = false;
  appProfileNames: Array<String>;
  constructor(private appProfilesService: AppProfilesService,
    private messageService: NdpMessageService,
    private router: Router,
    private route: ActivatedRoute, private formBuilder: FormBuilder) { }
  get f() { return this.mainForm.controls; }
  ngOnInit() {

    this.appProfilesService.list().subscribe(template => {
      this.appProfileNames = new Array<String>();
      template.responsePayload.forEach(app => {
        this.appProfileNames.push(app.appProfileName);
      });
      console.log('disallowedNames ', this.appProfileNames);
    });

    this.mainForm = this.formBuilder.group({
      appProfileId: [null],
      appProfileName: [null, [Validators.required, Validators.maxLength(25), this.nonDuplicte()]],
      description: [null, [Validators.required, Validators.maxLength(500),]],
      maximumDailyQuota: [null, [Validators.required, Validators.min(1)]],
      timeWindowStart: [null],
      timeWindowEnd: [null],
      maxThroughput: [null, [Validators.required, Validators.min(1)]],
      numberOfSessions: [null, [Validators.required, Validators.min(1)]],
      requiresDeliveryReport: [false, Validators.required],
      canAddFakeSenders: [false, Validators.required],
      // appProfileNameEdit : new FormControl()
    }, {
      validator: [this.greaterValidator('timeWindowEnd', 'timeWindowStart')
        , this.requiredValidator('timeWindowEnd', 'timeWindowStart'), this.requiredValidator('timeWindowStart', 'timeWindowEnd')]
    });

    if (this.appProfileID == null && sessionStorage.getItem('sessionAppProfileId') != null) {
      this.appProfileID = +sessionStorage.getItem('sessionAppProfileId');
      sessionStorage.removeItem('sessionAppProfileId');
    }
    if (this.appProfileID != null) {
      this.appProfile = new AppProfileModel();
      this.appProfile.appProfileId = this.appProfileID;
      this.appProfilesService.get(this.appProfile).subscribe(res => {
        this.appProfile = res.responsePayload;
        debugger;
        this.mainForm.setValue({
          appProfileId: this.appProfileID,
          appProfileName: this.appProfile.appProfileName,
          description: this.appProfile.description,
          maximumDailyQuota: this.appProfile.maximumDailyQuota,
          timeWindowStart: this.appProfile.timeWindowStart ? new Date(this.appProfile.timeWindowStart) : null,
          timeWindowEnd: this.appProfile.timeWindowStart ? new Date(this.appProfile.timeWindowEnd) : null,
          maxThroughput: this.appProfile.maxThroughput,
          numberOfSessions: this.appProfile.numberOfSessions,
          requiresDeliveryReport: this.appProfile.requiresDeliveryReport,
          canAddFakeSenders: this.appProfile.canAddFakeSenders,
        });
        //this.mainForm.setValidators(this.greaterValidator('timeWindowEnd', 'timeWindowStart'));
      });
      debugger;
      this.mainForm.get('appProfileName').clearValidators();
      this.mainForm.get('appProfileName').setValidators(Validators.required);
      this.mainForm.get('appProfileName').markAsDirty();
    } else {
      this.appProfile = new AppProfileModel();
    }

  }

  nonDuplicte(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: boolean } | null => {
      let found = false;
      if (this.appProfileNames) {
        this.appProfileNames.forEach(name => {
          if (name.trim().toLowerCase() === control.value.trim().toLowerCase()) {
            found = true;
          }
        });
        if (found) {
          return { 'duplicate': true };
        }
        return null;
      }
    };
  }

  checkBeforeDate(): boolean {
    let start = new Date(this.mainForm.get("timeWindowStart").value);
    let end = new Date(this.mainForm.get("timeWindowEnd").value);
    if (start >= end) { return false; }
    return true;

  }

  createAppProfile() {
    this.appProfilesService.scrollIfFormHasErrors(this.mainForm);
    for (const i in this.mainForm.controls) {
      if (this.mainForm.controls[i]) { this.mainForm.controls[i].markAsTouched(); }
    }
    this.submitted = true;
    // console.log(this.appProfile);
    if (this.mainForm.invalid) {
      return;
    }

    this.appProfilesService.create(this.mainForm.value).subscribe(res => {
      if (res.status === 0) {
        this.router.navigate(['appProfile']);
      }
    });


  }

  updateAppProfile() {
    this.appProfilesService.scrollIfFormHasErrors(this.mainForm);
    for (const i in this.mainForm.controls) {
      if (this.mainForm.controls[i]) { this.mainForm.controls[i].markAsTouched(); }
    }
    this.submitted = true;
    if (this.mainForm.invalid) {
      return;
    }

    this.appProfilesService.update(this.mainForm.value).subscribe(res => {
      if (res.status === 0) {
        this.router.navigate(['appProfile']);
      }
    });



  }

  greaterValidator(endControlName: string, startControlName: string): ValidatorFn {
    return (formGroup: FormGroup): ValidationErrors => {
      const control = formGroup.controls[endControlName];
      const matchingControl = formGroup.controls[startControlName];
      if (control.value !== null) {
        const options = { hour12: false };
        const startTime = new Date(control.value);
        const endTime = new Date(matchingControl.value);
        console.log('startTime => ',startTime.toLocaleTimeString('en-EG', options));
        console.log('endTime => ',endTime.toLocaleTimeString('en-EG', options));
        if (startTime.toLocaleTimeString('en-EG', options) <= endTime.toLocaleTimeString('en-EG', options)) {
          control.setErrors({ mustBeAfter: true });
        } else {
          let errors = control.errors;
          if (errors && errors['mustBeAfter']) {
            delete errors['mustBeAfter'];
            if (Object.keys(errors).length === 0) {
              errors = null;
            }
          }
          control.setErrors(errors);
        }
      }
      return;
    };
  }

  requiredValidator(control1: string, control2: string): ValidatorFn {
    return (formGroup: FormGroup): ValidationErrors => {
      const control = formGroup.controls[control1];
      const matchingControl = formGroup.controls[control2];
      if (control.value != null && matchingControl.value == null) {
        matchingControl.markAsTouched();
        matchingControl.setErrors({ required: true });
      } else {
        let errors = matchingControl.errors;
        if (errors && errors['required']) {
          delete errors['required'];
          if (Object.keys(errors).length === 0) {
            errors = null;
          }
        }
        matchingControl.setErrors(errors);
      }
      return;
    };
  }

  cancel() {
    this.router.navigate(['appProfile']);
  }
}
