import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';
import { EntityModel } from '../models/entity-model';

@Pipe({
    name: 'entityStatusFilter'
})
export class EntityStatusFilter implements PipeTransform {
    transform(entities: EntityModel[], status: boolean): any {
        if (status == null) {
            return entities;
        }
        const filtered = [];
        for (let i = 0; i < entities.length; i++) {
            const entity = entities[i];
            if (entity.active == status) {
                filtered.push(entity);
            }
        }
        return filtered;
    }
}
