import { Injectable } from '@angular/core';
import { BaseService } from '../shared/services/base-service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { Defines } from '../shared/static/defines';
import { ApiRequest } from '../shared/models/api-request';
import { map } from 'rxjs/operators';
import { RoleModel } from '../shared/models/role-model';
import { PrivilegeModel } from '../shared/models/privilege-model';
import { LookupModel } from '../shared/models/lookup-model';

@Injectable({
  providedIn: 'root'
})
export class RoleManagerService extends BaseService {
  constructor(private client: HttpClient) {
    super(client);
  }
  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.ROLES;
    console.log("Authorization Url =>", this.authorizationUrl);
  }
  public getAllRoles(): Observable<ApiResponse<Array<RoleModel>>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.ROLES + Defines.API_ENDPOINTS.list;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<RoleModel>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);
    return obs;
  }

  public getRole(id: number): Observable<ApiResponse<RoleModel>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.ROLES + Defines.API_ENDPOINTS.get;
    const obs: Observable<any> = this.http.post<ApiResponse<RoleModel>>
      (apiUrl, new ApiRequest(id, ''), this.httpOptions);
    return obs;
  }

  public deleteRole(id: number): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.ROLES + Defines.API_ENDPOINTS.deleterole;
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(id, ''), this.httpOptions);
    return obs;
  }


  public getAllPrivilege(): Observable<ApiResponse<Array<PrivilegeModel>>> {
    //debugger;
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.loadPrivilege;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<PrivilegeModel>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);
    // .pipe(map((apiResponse: any) => new ApiResponse()));
    return obs;
  }

  public getAllLevel(): Observable<ApiResponse<Array<LookupModel>>> {
    //debugger;
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.ROLES + Defines.API_ENDPOINTS.getaccesslevel;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<LookupModel>>>
      (apiUrl, new ApiRequest(null, ""), this.httpOptions);
    // .pipe(map((apiResponse: any) => new ApiResponse()));
    return obs;
  }



  public editRole(role: RoleModel): Observable<ApiResponse<string>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.ROLES + Defines.API_ENDPOINTS.editrole;
    const obs: Observable<any> = this.http.post<ApiResponse<string>>
      (apiUrl, new ApiRequest(role, ''), this.httpOptions);
    return obs;
  }


  public addRole(role: RoleModel): Observable<ApiResponse<number>> {
    //debugger;
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.ROLES + Defines.API_ENDPOINTS.insertrole;
    let obs: Observable<any> = this.http.post<ApiResponse<number>>
      (apiUrl, new ApiRequest(role, ""), this.httpOptions);
    // .pipe(map((apiResponse: any) => new ApiResponse()));
    return obs;
  }


}
