import { Component, OnInit } from '@angular/core';
import { User } from '../../shared/models/user-model';
import { SignUpService } from '../sign-up.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, AbstractControl, FormControl } from '@angular/forms';
import { map } from 'rxjs/operators';
import { Defines } from '../../shared/static/defines';

@Component({
  selector: 'ndp-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  user: User = new User();
  formInitComplete = false;
  form: FormGroup;

  constructor(private signUpService: SignUpService, private router: Router,
    private formbuilder: FormBuilder) { }

  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.formInitComplete = false;

    this.form = this.formbuilder.group({
      userName: ['', [Validators.required, Validators.pattern('^[a-zA-Z][a-zA-Z0-9_.-]{1,24}$')]],
      fName: ['', [Validators.required, Validators.pattern("^[a-zA-Z][a-zA-Z .'-]{1,24}$")]],
      lName: ['', [Validators.required, Validators.pattern("^[a-zA-Z][a-zA-Z .'-]{1,24}$")]],
      address: new FormControl(),
      email: ['', [Validators.required, Validators.email]],
      msisdn: ['', [Validators.pattern('[+]?[0][1][1|0|2|5][0-9]{8}')]]
    })
    this.formInitComplete = true;
  }

  checkUsernameExists(username: string) {
    this.signUpService.checkUsernameExists(username).subscribe(res => {
      if (res.errors.length > 0 || res.status != 0)
        return true;
      return false;
    });
  }

  checkEmailExists(email: string) {
    this.signUpService.checkEmailExists(email).subscribe(res => {
      if (res.errors.length > 0 || res.status != 0)
        return true;
      return false;
    });
  }

  submitUser() {
    this.signUpService.createUser(this.user).subscribe(user => {
      console.log("Create user ==> ", user.responsePayload);
      if(user.status ==0 )
      this.router.navigateByUrl('login');
    });
  }

  validateUser() {
    this.signUpService.checkUsernameExists(this.user.userName).subscribe(username => {
      if (username.errors.length > 0 || username.status != 0) {
        console.log(username.errors.length);
        return false;
      }
      else {
        this.signUpService.checkEmailExists(this.user.email).subscribe(email => {
          if (email.errors.length > 0 || email.status != 0) {
            console.log(email.errors.length);
            return false;
          }
          else {
            return true;
          }
        });
      }

    });
  }

  cancel() {
    this.router.navigate(['/login']);
  }
}
