
import { tap } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpHandler, HttpRequest, HttpEvent, HttpResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable, of } from 'rxjs';

import { ApiRequest } from '../models/api-request';
import { Defines } from '../static/defines';
import { ApiResponse } from '../models/api-response';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
    private interceptedRequest: any;
    intercept(
        req: HttpRequest<any>,
        next: HttpHandler
    ): Observable<HttpEvent<any>> {
        this.doPrework(req);
        let handleObs: Observable<HttpEvent<any>>;
        handleObs = next.handle(this.interceptedRequest);
        return handleObs.pipe(tap(response => {
            if (response instanceof HttpResponse) {
                this.doPostwork(response);
            }
        }));
    }

    constructor(private route: Router) {

    }

    private doPrework(req: HttpRequest<any>) {
        this.interceptedRequest = req.clone();
        if (!req.url.includes(Defines.API_ENDPOINTS.LOGIN)) {
            this.interceptedRequest.body
                = new ApiRequest(req.body.request, Defines.CONSTANTS.tokenPrefix + localStorage.getItem(Defines.SESSION_KEYS.authToken));
        } else {
            // console.log('Request is Login, no token involved');
        }
    }

    private doPostwork(resp: HttpResponse<any>) {
        if (resp.body) {
            let apiResponse: ApiResponse<any> = resp.body;
            if (apiResponse.status === Defines.B2B_CODES.InvalidUserOrpassword
                || apiResponse.status === Defines.B2B_CODES.authenticationError
            ) {
                localStorage.removeItem(Defines.SESSION_KEYS.authToken);
                localStorage.removeItem(Defines.SESSION_KEYS.userModel);
                this.route.navigate(['/login']);
            } else if (apiResponse.status === Defines.B2B_CODES.authorizationError) {
                // this.route.navigate(['/notauthorized']);
            }
        }
    }
}
