import { Component, OnInit } from '@angular/core';
import { MenuModule } from 'primeng/menu';
import { MenuItem } from 'primeng/api';
import { MenuItemModel } from 'src/app/shared/models/menu-item-model';
import { User } from 'src/app/shared/models/user-model';
import { Defines } from 'src/app/shared/static/defines';
import { OrderModule } from 'ngx-order-pipe';


@Component({
  selector: 'ndp-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  items: MenuItemModel[];
  user: User;
  //sorting
  key: string = 'label';
  reverse: boolean = false;
  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }
  constructor() { }
  display: boolean = true;
  ngOnInit() {
    //debugger;
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    this.items = this.user.menuItemModels;
    console.log("this is item menus => ", this.items);
  }

  toggleSidebar() {
    if (this.display == true) {
      this.display = false;
    } else {
      this.display = true;
    }
  }

  checkUserCanAddCampaign() {
    let found = false;
    this.user.privilegeList.forEach(p => {
      if (p.privilegeId === 16) {
        found = true;
      }
    });
    return found;
  }


}
