import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import { GroupService } from '../group.service';
import { GroupModel } from 'src/app/shared/models/group-model';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { Router } from '@angular/router';
import { MessageService } from 'primeng/api';
import { GroupMsisdnModel } from 'src/app/shared/models/group-msisdn-model';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv'
import 'rxjs/add/operator/toPromise';
import { ExportedDialModel } from 'src/app/shared/models/export-dials-model';
import { Defines } from 'src/app/shared/static/defines';
import { User } from 'src/app/shared/models/user-model';

@Component({
  selector: 'ndp-group-list',
  templateUrl: './group-list.component.html',
  styleUrls: ['./group-list.component.css'],
  providers: [MessageService]
})

export class GroupListComponent implements OnInit {
  groups: GroupModel[];
  filteredGroups: GroupModel[];

  selectedGroup: GroupModel;
  dropDown: boolean;
  dropDownClick: number;
  dropDownId: number;
  p: number = 1;
  perPage: number = 10;

  dialsPage: number = 1;
  perDialsPage: number = 10;

  filter: string;
  visibleSidebar: boolean = false;

  msisdnSearch: string;
  showExtenedMsisdn: boolean;
  expandedMsisdn: string;
  groupMsisdnList: GroupMsisdnModel[];

  exportedList: ExportedDialModel[];
  csvOptions = {
    fieldSeparator: ',',
    quoteStrings: '',
    decimalseparator: '.',
    showLabels: false,
    showTitle: false,
    useBom: true,
    noDownload: false,
  };
  csvFailedOptions = {
    fieldSeparator: '',
    quoteStrings: '',
    decimalseparator: '.',
    showLabels: false,
    showTitle: false,
    useBom: true,
    noDownload: false,
  };
  deleteConfirm = false;
  deleteConfirmId: number;
  //Authorization Vars
  authorizationUrl: string;
  defines = Defines.API_CRUD;
  //Access Levels attributes
  user: User;
  listAccessLevel: number = 0;
  editAccessLevel: number = 0;
  deleteAccessLevel: number = 0;
  deleteDialsAccessLevel: number = 0;
  exportDialsAccessLevel: number = 0;
  canCreate: boolean;

  constructor(private groupService: GroupService, private ndpMessageService: NdpMessageService,
    private router: Router, private messageService: MessageService) { }

  ngOnInit() {
    console.log("this.authorizationUrl = this.groupService.authorizationUrl => ", this.groupService.authorizationUrl);
    this.authorizationUrl = this.groupService.authorizationUrl;
    this.setAccessLevel();
    this.listGroup();

  }

  setAccessLevel() {
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    this.user.privilegeList.forEach(rolePrivilege => {
      switch (rolePrivilege.privilegeId) {
        case Defines.PRIVILEGES.GROUP.EDIT: {
          this.editAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.GROUP.LIST: {
          this.listAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.GROUP.DELETE: {
          this.deleteAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.GROUP.CREATE: {
          this.canCreate = true;
          break;
        } case Defines.PRIVILEGES.GROUP.DELETE_DIALS: {
          this.deleteDialsAccessLevel = rolePrivilege.accessLevel;
          break;
        } case Defines.PRIVILEGES.GROUP.EXPORT_DIALS: {
          this.exportDialsAccessLevel = rolePrivilege.accessLevel;
          break;
        }
      }
    }
    )
  }

  listGroup(): void {
    this.groupService.listGroupPromise().then((data: any) => {
      this.groups = new Array<GroupModel>();
      this.groups = data.responsePayload;

      this.groups.forEach(group => {
        if (group.createdBy === this.user.id) {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            group.canEdit = true;
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            group.canDelete = true;
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            group.canView = true;
          } if (this.deleteDialsAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            group.canDeleteDials = true;
          }
          if (this.exportDialsAccessLevel >= Defines.ACCESS_LEVELS.OWN) {
            group.canExport = true;
          }
        } else if (group.ownerEntity === this.user.entityId) {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            group.canEdit = true;
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            group.canDelete = true;
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            group.canView = true;
          } if (this.deleteDialsAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            group.canDeleteDials = true;
          }
          if (this.exportDialsAccessLevel >= Defines.ACCESS_LEVELS.ENTITY) {
            group.canExport = true;
          }
        } else {
          if (this.editAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            group.canEdit = true;
          }
          if (this.deleteAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            group.canDelete = true;
          }
          if (this.listAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            group.canView = true;
          } if (this.deleteDialsAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            group.canDeleteDials = true;
          }
          if (this.exportDialsAccessLevel >= Defines.ACCESS_LEVELS.EVERYTHING) {
            group.canExport = true;
          }
        }
      });

      this.filteredGroups = this.groups;
    }
    );
  }

  deleteGroup(): void {
    const index = this.groups.findIndex(group => this.selectedGroup.groupId === group.groupId);
    const groupToDelete = this.groups[index];
    this.groupService.delete(this.selectedGroup.groupId).subscribe(resp => {
      if (resp.status === 0) {
        this.groups = this.groups.filter(x => x.groupId !== groupToDelete.groupId);
      }
    });
    this.dropDownClose();
  }

  showConfirm(event) {
    debugger;
    this.deleteConfirm = true;
    this.deleteConfirmId = this.selectedGroup.groupId;
    event.stopPropagation();
  }

  onConfirm() {
    this.deleteConfirm = false;
    this.deleteConfirmId = null;
    this.deleteGroup();
    this.dropDownClose();
  }

  onReject() {
    this.deleteConfirm = false;
    this.deleteConfirmId = null;
    this.dropDownClose();
  }

  dropDownOpen(group: GroupModel) {
    console.log('opening', group);
    this.selectedGroup = group;
    this.dropDownId = group.groupId;
    this.dropDownClick = 0;
    this.dropDown = true;
  }

  dropDownClose() {
    this.selectedGroup = null;
    this.dropDown = false;
  }

  hideDropDown() {
    this.dropDownClick++;
    if (this.dropDown && this.dropDownClick > 1) {
      this.dropDown = false;
      this.dropDownId = null;
    }
  }

  update(event) {
    console.log("Anahooo");
    this.router.navigateByUrl('/groups/get/' + this.selectedGroup.groupId);
    event.stopPropagation();
  }

  create() {
    this.router.navigateByUrl('/groups/create');
  }


  sidenavOpened(group: GroupModel) {
    this.selectedGroup = group;
    this.visibleSidebar = true;
  }

  sidenavClosed() {
    this.selectedGroup = null;
    this.visibleSidebar = false;
    this.groupMsisdnList = null;
    this.msisdnSearch = null;
  }

  searchDials() {
    let requestModel: GroupMsisdnModel = new GroupMsisdnModel();
    requestModel.groupId = this.selectedGroup.groupId;
    requestModel.msisdn = this.msisdnSearch;
    this.groupService.searchDials(requestModel).subscribe(response => this.groupMsisdnList = response.responsePayload);
  }

  openExpandMsisdn(expandedMsisdn: string) {
    this.expandedMsisdn = expandedMsisdn;
    this.showExtenedMsisdn = true;
  }

  cancelDeleteMsisdn() {
    this.expandedMsisdn = null;
    this.showExtenedMsisdn = false;
  }

  deleteMsisdn() {
    let requestModel: GroupMsisdnModel = new GroupMsisdnModel();
    requestModel.groupId = this.selectedGroup.groupId;
    requestModel.msisdn = this.expandedMsisdn;
    this.groupService.deleteDial(requestModel).subscribe();
    let index;
    debugger;
    for (let entry of this.groupMsisdnList) {
      if (entry.msisdn === this.expandedMsisdn) {
        index = this.groupMsisdnList.indexOf(entry);
      }
    }

    if (index != -1) {
      this.groupMsisdnList.splice(index, 1);
    }
    this.showExtenedMsisdn = false;
  }

  downloadCSV(group: GroupModel) {
    this.groupService.getAllDials(group.groupId).then((data: any) =>
      this.downloud(group, data.responsePayload));
  }

  downloadFailedCSV(group: GroupModel) {
    this.groupService.getAllFailedDials(group.groupId).then((data: any) => this.downloudFailedDials(group, data.responsePayload));
  }

  downloud(group: GroupModel, exportedList: ExportedDialModel[]) {
    console.log("List is =>", exportedList);
    if (exportedList != null && exportedList.length != 0) {
      exportedList.forEach(dial => {
        if (dial.parameters === null) {
          delete dial["parameters"];
        }
      })
      new AngularCsv(exportedList, group.groupName, this.csvOptions)
    } else {
      this.ndpMessageService.addWarnings(["There is no dials to export"]);
    }
  }

  downloudFailedDials(group: GroupModel, exportedList: string[]) {
    if (exportedList != null && exportedList.length != 0) {
      console.log("exportedList => ", exportedList);
      new AngularCsv(exportedList, group.groupName, this.csvFailedOptions)
    } else {
      // this.ndpMessageService.addWarnings(["There is no dials to export"]);
    }
  }

  setFilteredGroups(filtered: GroupModel[]) {
    this.filteredGroups = filtered;
  }
}
