import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';

@Pipe({
    name: 'userentityFilter'
})
export class UsersEntityFilter implements PipeTransform {
    transform(users: AdminViewUser[], businessEntityName: string): any {

        if (businessEntityName == null || businessEntityName.length < 3) {
            return users;
        }
        const filtered = [];
        for (let i = 0; i < users.length; i++) {
            const user = users[i];
            if (user.entityName != null && user.entityName.toLowerCase().includes(businessEntityName.toLowerCase())) {
                filtered.push(user);
            }
        }
        return filtered;
    }
}
