import { Injectable, OnInit } from '@angular/core';
import { Message } from 'primeng/api';
import { Observable } from 'rxjs';
import { Defines } from '../static/defines';

@Injectable({
  providedIn: 'root'
})
export class NdpMessageService {


  private messagesObservable: Observable<Array<Message>>;
  // private successObservable: Observable<Array<Message>>;
  // private warningObservable: Observable<Array<Message>>;
  // private infoObservable: Observable<Array<Message>>;

  private messagesObserver: any;
  // private infoObserver: any;
  // private warningObserver: any;
  // private successObserver: any;
  constructor() {
    console.log("all arrays initialized");
    this.intitalizeObservables();
  }


  intitalizeObservables() {
    this.messagesObservable = Observable.create(observer => {
      console.log("Observer ===>",observer);
      this.messagesObserver = observer;
    });
    // this.infoObservable = Observable.create(observer => {
    //   this.infoObserver = observer;
    // });
    // this.successObservable = Observable.create(observer => {
    //   this.successObserver = observer;
    // });
    // this.warningObservable = Observable.create(observer => {
    //   this.warningObserver = observer;
    // });
  }

  allMessages(): Observable<Array<Message>> {
    return this.messagesObservable;
  }


  // getWarningMessages(): Observable<Array<Message>> {
  //   return this.warningObservable;
  // }
  // getInfoMessages(): Observable<Array<Message>> {
  //   return this.infoObservable;
  // }
  // getSuccessMessages(): Observable<Array<Message>> {
  //   return this.successObservable;
  // }

  addError(msgSummary: string, msgDetail: string) {
    let errorMessages = new Array<Message>();
    errorMessages.push({
      severity: Defines.MSG_SEVERITY.error,
      summary: msgSummary,
      detail: msgDetail,
      life: 10000
    });
    console.log("Emmitting errorMessages",errorMessages);
    this.messagesObserver.next(errorMessages);
  }
  addErrors(messages: string[]) {
    let errorMessages = new Array<Message>();
    messages.forEach(msg => errorMessages.push({
      severity: Defines.MSG_SEVERITY.error,
      summary: msg,
      detail: undefined,
      life: 10000
    }));
    this.messagesObserver.next(errorMessages);
  }

  addWarnings(messages: string[]) {
    let warnings = new Array<Message>();
    messages.forEach(msg => warnings.push({
      severity: Defines.MSG_SEVERITY.warn,
      summary: msg,
      detail: undefined,
      life: 10000
    }));
    this.messagesObserver.next(warnings);
  }
  addInfos(messages : string[]) {
    let infoMsgs = new Array<Message>();
    messages.forEach(msg => infoMsgs.push({
      severity: Defines.MSG_SEVERITY.success,
      summary: msg,
      detail: undefined,
      life: 3000
    }));
    this.messagesObserver.next(infoMsgs);
  }

  addInfo(message : string) {
    let infoMsgs = new Array<Message>();
    infoMsgs.push({
      severity: Defines.MSG_SEVERITY.success,
      summary: message,
      detail: undefined,
      life: 3000
    });
    this.messagesObserver.next(infoMsgs);
  }
  addSuccess(...messages: string[]) {
    let successMsgs = new Array<Message>();
    messages.forEach(msg => successMsgs.push({
      severity: Defines.MSG_SEVERITY.success,
      summary: msg,
      detail: undefined,
      life: 3000
    }));
    this.messagesObserver.next(successMsgs);
  }

  
}
