
import { catchError } from 'rxjs/operators';
import { Component, OnInit, Input } from '@angular/core';
import { LoginServiceService } from '../login-service.service';
import { error } from '@angular/compiler/src/util';
import { AuthToken } from 'src/app/shared/models/auth-token';
import { Defines } from 'src/app/shared/static/defines';
import { Router } from '@angular/router';
import { PlatformLocation } from '@angular/common';
import { ApiResponse } from 'src/app/shared/models/api-response';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'ndp-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [LoginServiceService]
})

export class LoginComponent implements OnInit {
  @Input() userAuthModel: AuthToken;
  userName: string;
  password: string;
  remember: boolean;

  etisalatLogo: string;
  loginImage: string;
  constructor(private route: Router, private loginService: LoginServiceService,
    private platformLocation: PlatformLocation, private formBuilder: FormBuilder,
    private cookieService: CookieService) { }

  loginForm: FormGroup;

  ngOnInit() {
    this.remember = false;
    if (localStorage.getItem(Defines.SESSION_KEYS.authToken) != null && localStorage.getItem(Defines.SESSION_KEYS.userModel) != null) {
      this.route.navigate(['home']);
    }

    this.loginImage = Defines.CONSTANTS.BASE_URL + 'assets/img/login-img.svg';
    // let baseUrl = document.getElementsByTagName("base")[0].href;
    console.log('baseHref ', Defines.CONSTANTS.BASE_URL);
    console.log('platformLocation => ', this.platformLocation);
    this.etisalatLogo = Defines.CONSTANTS.BASE_URL + 'assets/img/etisalat-logo01 copy.png';
    // localStorage.removeItem(Defines.SESSION_KEYS.authToken);
    // localStorage.removeItem(Defines.SESSION_KEYS.userModel);

    this.loginForm = this.formBuilder.group({
      userName: ['', [Validators.required]],
      password: ['', [Validators.required]]
    });
    if (this.cookieService.check('usr') && this.cookieService.check('pas')) {
      this.loginForm.patchValue({
        userName: atob(atob(atob(this.cookieService.get('usr')))),
        password: atob(atob(this.cookieService.get('pas')))
      });
      this.remember = true;
    }
  }

  doLogin() {

    if (this.loginForm.invalid) {
      this.loginService.markFormGroupTouched(this.loginForm);
      return;
    }
    this.userName = this.loginForm.get('userName').value;
    this.password = this.loginForm.get('password').value;
    this.loginService.sendLoginRequest(this.userName, this.password).pipe(catchError((err, caught) => {
      console.log('Error in doLogin ', err, caught);
      throw err;
    }
    )).subscribe(

      apiResponse => {
        console.log('apiResponse Received ==> ', apiResponse);
        if (apiResponse.status === Defines.API_RESPONSES.success) {
          const authToken:AuthToken = new AuthToken(apiResponse.responsePayload.token, apiResponse.responsePayload.user);
          localStorage.setItem(Defines.SESSION_KEYS.authToken, (<AuthToken>authToken).token);
          localStorage.setItem(Defines.SESSION_KEYS.userModel, (JSON.stringify((authToken).user)));
          if (this.remember === true) {
            this.cookieService.set('usr', btoa(btoa(btoa(this.userName))));
            this.cookieService.set('pas', btoa(btoa(this.password)));
          } else {
            this.cookieService.delete('usr');
            this.cookieService.delete('usr');
          }
          this.route.navigate(['home']);
        } else if (apiResponse.status === Defines.API_RESPONSES.EXPIRED_PASSWORD) {
          const url: string = 'reset-expiry-password/' + apiResponse.responsePayload;
          this.route.navigate([url]);
        } else {
          localStorage.removeItem(Defines.SESSION_KEYS.authToken);
          localStorage.removeItem(Defines.SESSION_KEYS.userModel);
        }
      }
    );
  }

  register() {
    this.route.navigate(['register']);
  }

  forget() {
    this.route.navigate(['forget-password']);
  }
  get lf() {
    return this.loginForm.controls;
  }

  keyDownFunction(event) {
    if (event.keyCode === 13) {
      this.doLogin();
    }
  }
}
