import { Component, OnInit } from '@angular/core';
import { SmsTemplateModel } from 'src/app/shared/models/sms-template-model';
import { SmsTemplatesService } from '../sms-templates.service';
import 'rxjs/add/operator/toPromise';
import { FormBuilder, FormGroup, Validators, AbstractControl, ValidatorFn } from '@angular/forms';
import { Defines } from 'src/app/shared/static/defines';

@Component({
  selector: 'ndp-sms-template-list',
  templateUrl: './sms-template-list.component.html',
  styleUrls: ['./sms-template-list.component.css']
})
export class SmsTemplateListComponent implements OnInit {
  smsTemplateList = new Array<SmsTemplateModel>();
  filteredList = null;
  initialized: boolean;

  smsForm: FormGroup;
  smsScriptLength = 0;
  editableTemplate: SmsTemplateModel;
  editableTemplateId: number;
  editTemplate: boolean;
  createTemplate: boolean;
  dropDown: boolean;
  dropDownSmsId: number;
  dropDownClick: number;
  dropDownId: number;

  deleteConfirm = false;
  deleteConfirmId: number;

  //Pagination Filtering & Sorting
  p: number = 1;
  perPage: number = 10;
  //filtering
  filter: string;
  //sorting
  key: string = 'title';
  reverse: boolean = false;

    //Authorization Vars
    authorizationUrl: string;
    defines = Defines.API_CRUD;
  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }
  constructor(private smsService: SmsTemplatesService, private formBuilder: FormBuilder) { }

  ngOnInit() {
    // Initialize Sms Listing
    this.authorizationUrl = this.smsService.authorizationUrl;
    this.createTemplate = false;
    this.editTemplate = false;
    this.initialized = false;
    this.listSms();

    this.smsForm = this.formBuilder.group({
      id: null,
      title: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(25), this.nameExists()]],
      text: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(160)]],
      language: [1],
      createdBy: 4
    });

    this.smsForm.controls.text.valueChanges.subscribe(val => {
      if (val) {
        this.smsScriptLength = val.length;
      } else {
        this.smsScriptLength = 0;
      }
    });
  }
  get sf() { return this.smsForm.controls; }
  resetPagination() {
    if (this.p > 1) {
      this.p = 1;
    }
  }

  confirmDelete(id: number) {
    this.deleteConfirm = true;
    this.deleteConfirmId = id;
  }
  hideConfirmDelete() {
    this.deleteConfirm = false;
    this.deleteConfirmId = null;
  }

  hideDropDown() {
    this.dropDownClick++;
    if (this.dropDown && this.dropDownClick > 1) {
      this.dropDown = false;
      /*this.dropDownSmsId = null;*/
    }
  }

  showDropDown(campaign: number) {
    this.dropDownClick = 0;
    this.dropDown = true;
  }

  listSms() {
    this.smsService.listSmsTemplates().subscribe(templates => {
      if (templates.responsePayload.length === 0) {
        this.smsTemplateList = new Array();
      }
      this.smsTemplateList = templates.responsePayload;
      this.filteredList = new Array<SmsTemplateModel>();
      this.filteredList = this.smsTemplateList;
    });
  }

  dropDownOpen(smsModel: SmsTemplateModel) {
    this.dropDownSmsId = smsModel.id;
    this.dropDownClick = 0;
    this.dropDown = true;
  }

  dropDownClose() {
    this.dropDownSmsId = null;
    this.dropDown = false;
  }

  clickedOut(event) {
    this.dropDownClose();
  }

  editSms(smsModel: SmsTemplateModel) {
    this.editableTemplateId = smsModel.id;
    this.smsForm.setValue({
      id: smsModel.id,
      title: smsModel.title,
      text: smsModel.text,
      language: smsModel.language,
      createdBy: smsModel.createdBy
    });
    this.smsForm.get('title').clearValidators();
    this.smsForm.get('title').setValidators([Validators.required, Validators.minLength(5), Validators.maxLength(40)]);

    this.editableTemplate = Object.assign({}, this.smsForm.value);
    this.editTemplate = true;
    this.dropDownClose();
  }

  deleteSms(smsModel: SmsTemplateModel) {
    this.smsService.deleteSmsTemplate(smsModel).subscribe(template => {
      if (template.status === 0) {
        this.smsTemplateList = this.smsTemplateList.filter(x => x.id !== smsModel.id);
      }
    });
    this.hideConfirmDelete();
    this.dropDownClose();
  }

  saveSms(smsModel: SmsTemplateModel) {
    if (this.smsForm.invalid) {
      this.smsForm.controls.title.markAsTouched();
      this.smsForm.controls.text.markAsTouched();
      return;
    }
    if (this.createTemplate) {
      this.smsService.createSmsTemplate(this.smsForm.value).subscribe(template => {

        if (template.warnings.length === 0) {
          console.log("Creating smsTemplate ==> ", template.responsePayload);
          smsModel = Object.assign({}, this.smsForm.value);
          this.smsTemplateList.push(smsModel);
          this.createTemplate = false;
          this.editableTemplate = null;
          this.editableTemplateId = null;
          this.listSms();
        }

      });
    }
    if (this.editTemplate) {
      this.smsService.updateSmsTemplate(this.smsForm.value).subscribe(template => {
        console.log("Updating smsTemplate ==> ", template.responsePayload);
        this.smsTemplateList[this.smsTemplateList.findIndex(record => record.id === this.editableTemplate.id)] = this.smsForm.value;
        this.editableTemplateId = null;
        this.editTemplate = false;
      });
    }
  }

  cancel() {
    if (this.editTemplate) {
      this.editableTemplateId = null;
      this.editTemplate = false;
      this.smsForm.controls.title.markAsUntouched();
      this.smsForm.controls.text.markAsUntouched();
    }

    if (this.createTemplate) {
      this.createTemplate = false;
      this.smsForm.markAsUntouched();
      this.smsForm.markAsPristine();
    }
  }

  createSms() {
    this.createTemplate = true;
    this.smsForm.reset();
    this.smsForm.setValue({
      id: null,
      title: null,
      text: null,
      language: 1,
      createdBy: 4
    });
    this.smsForm.markAsUntouched();
    this.smsForm.markAsPristine();
    this.smsForm.get('title').setValidators([Validators.required, Validators.minLength(5), Validators.maxLength(40), this.nameExists()]);

  }

  nameExists(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: boolean } | null => {
      let found = false;
      this.smsTemplateList.forEach(sms => {
        if (control.value != null  && sms.title.trim().toLowerCase() === control.value.trim().toLowerCase()) {
          found = true;
        }
      });
      if (found) {
        return { 'smsExists': true };
      }
      return null;
    };
  }

  setFilteredList(filtered: SmsTemplateModel[]) {
    this.filteredList = filtered;
  }
}
