import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';
import { EntityModel } from '../models/entity-model';
import { CampaignModel } from '../models/campaign-model';
import { AppProfileModel } from '../models/app-profile-model';

@Pipe({
    name: 'appProfileAppProfileNameFilter'
})
export class AppProfileAppProfileNameFilter implements PipeTransform {
    transform(appProfiles: AppProfileModel[], name: string): any {
        if (appProfiles == null || name == null || name.length < 3) {
            return appProfiles;
        }
        const filtered = [];
        for (let i = 0; i < appProfiles.length; i++) {
            const appProfile = appProfiles[i];
            if (appProfile.appProfileName != null && appProfile.appProfileName.toLowerCase().includes(name.toLowerCase())) {
                filtered.push(appProfile);
            }
        }
        return filtered;
    }
}
