import { Component, OnInit } from '@angular/core';
import { AccountService } from '../account.service';
import { Router } from '@angular/router';
import { AdminViewUser } from 'src/app/shared/models/admin-view-user-model';
import { Defines } from 'src/app/shared/static/defines';

@Component({
  selector: 'ndp-users-accounts-list',
  templateUrl: './users-accounts-list.component.html',
  styleUrls: ['./users-accounts-list.component.css']
})
export class UsersAccountsListComponent implements OnInit {

  expandedUserId: number;
  usersAccounts = new Array<AdminViewUser>();
  usersAccountsToShow = null;
  dropDownUserId: number;
  dropDown: boolean;
  inialized = false;

  deleteConfirm = false;
  deletedId: number;

  //Filter Menu
  filterMenuEnabled: boolean;

  //Pagination Filtering & Sorting
  p: number = 1;
  perPage: number = 10;

  //filtering
  usernameToSearch: string;
  statusFilter: boolean;
  activeDirectoryUserFilter: boolean;
  businessEntityToSearch: string;

  //sorting
  key: string = 'fName';
  reverse: boolean = false;

  //Authorization Vars
  authorizationUrl: string;
  defines = Defines.API_CRUD;

  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }
  cachedUserAccountId = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel)).id;

  constructor(private accountService: AccountService, private router: Router) { }

  ngOnInit() {
    this.authorizationUrl = this.accountService.authorizationUrl;
    this.activeDirectoryUserFilter = null;
    this.statusFilter = null;
    this.listUsersAccounts();
  }

  listUsersAccounts() {
    this.accountService.listUsersAccounts().subscribe(res => {
      this.usersAccounts = res.responsePayload;
      this.inialized = true;
    });
  }

  addUser() {
    this.router.navigate(["user/add"]);
  }
  showHideFilterMenu() {
    this.filterMenuEnabled = !this.filterMenuEnabled;
  }

  setUsersToShow(usersToShow: AdminViewUser[]) {
    this.usersAccountsToShow = usersToShow;
    // console.log("usersToShow", this.usersAccountsToShow);
  }

  dropDownMenu(event: Event, user: AdminViewUser) {
    
    console.log("dropDownMenudropDownMenu");
    event.stopPropagation();
    if (!this.dropDown || this.dropDownUserId != user.id) {
      this.dropDownUserId = user.id;
      this.dropDown = true;
    } else {
      this.dropDown = false;
    }
  }
  editUser(user: AdminViewUser) {
    //this.router.navigate(["admin/addeditaccount"],{queryParams: {userId: user.id}});
    this.router.navigateByUrl('user/edit/' + user.id);
  }

  deleteUser(event: Event, user: AdminViewUser) {
    this.accountService.deleteUser(user.id).subscribe(res => {
      if (res.responsePayload > 0) {
        // let indexToDelete = this.usersAccounts.indexOf(user);
        // this.usersAccounts.splice(indexToDelete, 1);
        // indexToDelete = this.usersAccountsToShow.indexOf(user);
        // this.usersAccountsToShow.splice(indexToDelete, 1);
        this.usersAccounts = this.usersAccounts.filter(x => x.id !== user.id);
      }
    });
  }
  showConfirmDelete(event: Event, user: AdminViewUser) {
    //event.stopPropagation();
    this.deleteConfirm = true;
    this.deletedId = user.id;
  }
  hideConfirmDelete(event: Event) {
    event.stopPropagation();
    this.deleteConfirm = false;
    this.deletedId = 0;
  }
  setExpandedUserId(event: Event, userId: number) {
    event.stopPropagation();
    this.dropDownClose();
    if (userId === this.expandedUserId) {
      this.expandedUserId = null;
    } else {
      this.expandedUserId = userId;
    }
  }
  closeExpandedUserId() {
    this.expandedUserId = null;
  }
  dropDownClose() {
    this.dropDownUserId = null;
    this.dropDown = false;
  }
  closeAllMenus() {
    this.closeExpandedUserId();
    this.dropDownClose();
  }
  getFirstLetter(word: string): string {
    return word.charAt(0).toUpperCase();
  }
}
