import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AccountService } from '../account.service';
import { FormBuilder, FormGroup, AbstractControl, Validators } from '@angular/forms';

@Component({
  selector: 'ndp-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {

  newPassword: string;
  confirmNewPassword: string;

  userForm: FormGroup;

  constructor(private accountService: AccountService, private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.userForm = this.formBuilder.group({
      newPassword: ['', [Validators.required,Validators.pattern("^(?=.*[A-Za-z])(?=.*[0-9])[A-Za-z0-9!@#$%^&*(),.?\":{}|<>\\/]{8,}$")]],
      confirmNewPassword: ['', Validators.required]
    }, {
        validator: this.passwordMatchingValidator('newPassword', 'confirmNewPassword')
      });
  }
  get uf() {
    return this.userForm.controls;
  }
  changePassword() {
    this.assignFormToPasswords();
    console.log(this.userForm.errors);
    if (!this.userForm.valid) {
      this.accountService.markFormGroupTouched(this.userForm);
      return;
    }
    this.accountService.savePassword(this.newPassword).subscribe(res => {
      this.router.navigate(['myprofile/view']);
    });


  }

  cancel() {
    this.router.navigate(['myprofile/view']);
  }
  passwordMatchingValidator(newPassword: string, confirmNewPassword: string) {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[newPassword];
      const matchingControl = formGroup.controls[confirmNewPassword];
      if (control.value != matchingControl.value) {
        matchingControl.setErrors({ mustMatch: true });
      } else {
        matchingControl.setErrors(null);
      }
    };
  }

  passwordValidator(control: AbstractControl): { [key: string]: boolean } | null {
    if (control.value !== undefined && control.value !== null) {
      return { 'notValid': true };
    }
    return null;
  }
  assignFormToPasswords() {
    this.newPassword = this.userForm.get('newPassword').value;
    this.confirmNewPassword = this.userForm.get('confirmNewPassword').value;
  }
}
