import { Pipe, PipeTransform } from '@angular/core';
import { AdminViewUser } from '../models/admin-view-user-model';
import { EntityModel } from '../models/entity-model';

@Pipe({
    name: 'entityTypeFilter'
})
export class EntityTypeFilter implements PipeTransform {
    transform(entities: EntityModel[], type: string): any {
        if (type == 'all') {
            return entities;
        }
        const filtered = [];
        for (let i = 0; i < entities.length; i++) {
            const entity = entities[i];
            if (entity.isInternal && type == 'internal') {
                filtered.push(entity);
            }
            if (!entity.isInternal && type == 'external') {
                filtered.push(entity);
            }
        }
        return filtered;
    }
}
