import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CampaignListComponent } from './campaign-list/campaign-list.component';
import { AddEditCampaignComponent } from './add-edit-campaign/add-edit-campaign.component';
import { CampaignService } from './campaign.service';
import { SharedModule } from '../shared/shared.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgxPaginationModule } from 'ngx-pagination';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { OrderModule } from 'ngx-order-pipe';
import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FileDropModule } from 'ngx-file-drop';
import { CampaignStatusFilter } from 'src/app/shared/Pipes/campaign-status-filter.pipe';
import { CampaignNameFilter } from '../shared/Pipes/campaign-name-filter.pipe';
import { ClickOutsideModule } from 'ng-click-outside';
import { ProgressBarModule } from 'primeng/progressbar';
import { DynamicDialogModule } from 'primeng/dynamicdialog';
import { TestSmsComponent } from './test-sms/test-sms.component';
import { CampaignStatsComponent } from './campaign-stats/campaign-stats.component';
import { CampaignDetailsComponent } from './campaign-details/campaign-details.component';
import { CampaignProgressComponent } from './campaign-progress/campaign-progress.component';
import { CampaignDateFilter } from '../shared/Pipes/campaign-date-filter.pipe';
import { AutosizeModule } from 'ngx-autosize';
import {TooltipModule} from 'primeng/tooltip';
import { CampaignEntityFilter } from '../shared/Pipes/campaign-entity-filter.pipe';


@NgModule({
  declarations: [
    CampaignListComponent,
    AddEditCampaignComponent,
    CampaignStatusFilter,
    CampaignNameFilter,
    CampaignDateFilter,
    CampaignEntityFilter,
    TestSmsComponent,
    CampaignStatsComponent,
    CampaignDetailsComponent,
    CampaignProgressComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    NgxPaginationModule,
    Ng2SearchPipeModule,
    OrderModule,
    OwlDateTimeModule,
    OwlNativeDateTimeModule,
    BrowserModule,
    BrowserAnimationsModule,
    FileDropModule,
    ReactiveFormsModule,
    ClickOutsideModule,
    ProgressBarModule,
    DynamicDialogModule,
    AutosizeModule,
    TooltipModule
  ], providers: [CampaignService],
  exports: [CampaignListComponent, AddEditCampaignComponent, CampaignStatsComponent],
  entryComponents: [
    TestSmsComponent
  ]
})
export class CampaignsModule { }
