import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { CampaignService } from '../campaign.service';
import { Defines } from 'src/app/shared/static/defines';
import { timer, Subscription } from 'rxjs';

@Component({
  selector: 'ndp-campaign-stats',
  templateUrl: './campaign-stats.component.html',
  styleUrls: ['./campaign-stats.component.css']
})
export class CampaignStatsComponent implements OnInit, OnDestroy {

  constructor(private campaignService: CampaignService) { }
  intervalId: any;
  countsMap: Map<number, number>;
  subscription: Subscription;
  @Input() refreshInterval: number;

  ngOnInit() {
    if (this.refreshInterval == null) {
      this.refreshInterval = 15000;
    }
    this.refreshData();
    this.intervalId = setInterval(() => { this.refreshData(); }, this.refreshInterval);
  }


  ngOnDestroy() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  }

  async refreshData() {
    this.subscription = this.campaignService.getCounts().subscribe(response => {
      this.countsMap = response.responsePayload;
    });
  }

  getRunning() {
    if (this.countsMap) {
      const handling = this.countsMap[Defines.CAMPAIGN_STATUS.HANDLING];
      const enqueued = this.countsMap[Defines.CAMPAIGN_STATUS.SMS_ENQUEUED];
      return (handling ? handling : 0) + (enqueued ? enqueued : 0);
    }
  }
  getPending() {
    if (this.countsMap) {
      const pending = this.countsMap[Defines.CAMPAIGN_STATUS.PENDING];
      return (pending ? pending : 0);
    }
  }
  getDone() {
    if (this.countsMap) {
      const success = this.countsMap[Defines.CAMPAIGN_STATUS.SUCCESS];
      return (success ? success : 0);
    }
  }
  getScheduled() {
    if (this.countsMap) {
      const ready = this.countsMap[Defines.CAMPAIGN_STATUS.READY];
      return (ready ? ready : 0);
    }
  }

  getPaused() {
    if (this.countsMap) {
      const paused = this.countsMap[Defines.CAMPAIGN_STATUS.PAUSED];
      return (paused ? paused : 0);
    }
  }

  getFailed() {
    if (this.countsMap) {
      const failed = this.countsMap[Defines.CAMPAIGN_STATUS.FAILED] + this.countsMap[Defines.CAMPAIGN_STATUS.ALL_MESSAGES_FAILED];
      return (failed ? failed : 0);
    }
  }

}
