import { Injectable } from '@angular/core';
import { Observable, observable, of } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { EntityModel } from '../shared/models/entity-model';
import { Defines } from '../shared/static/defines';
import { BaseService } from '../shared/services/base-service';
import { ApiRequest } from '../shared/models/api-request';
import { HttpClient } from '@angular/common/http';
import { AppProfileModel } from '../shared/models/app-profile-model';
import { GroupModel } from '../shared/models/group-model';
import { tap, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class EntityServiceService extends BaseService {
  modeltoEdit: EntityModel;
  mode=0;
  constructor(private client: HttpClient) {
    super(client);
  }
  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.entity;
    console.log("Authorization Url =>", this.authorizationUrl);
}

  public getAllEntity(): Observable<ApiResponse<Array<EntityModel>>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.entity + Defines.API_ENDPOINTS.LIST;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<EntityModel>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);

    return obs;
  }

  public getEntity(id: number): Observable<ApiResponse<EntityModel>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.entity + Defines.API_ENDPOINTS.GET;
    const obs: Observable<any> = this.http.post<ApiResponse<EntityModel>>
      (apiUrl, new ApiRequest(id, ''), this.httpOptions);
    return obs;
  }

  public getAllAppProfile(): Observable<ApiResponse<Array<AppProfileModel>>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.appprofile + Defines.API_ENDPOINTS.LIST;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<AppProfileModel>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);

    return obs;
  }


  public addEntity(entity: EntityModel): Observable<ApiResponse<string>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.entity + Defines.API_ENDPOINTS.CREATE;
    let obs: Observable<any> = this.http.post<ApiResponse<string>>
      (apiUrl, new ApiRequest(entity, ''), this.httpOptions);

    return obs;

  }


  public editEntity(entity: EntityModel): Observable<ApiResponse<string>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.entity + Defines.API_ENDPOINTS.UPDATE;
    let obs: Observable<any> = this.http.post<ApiResponse<string>>
      (apiUrl, new ApiRequest(entity, ''), this.httpOptions);

    return obs;

  }

  public deleteEntity(entityId: number): Observable<ApiResponse<string>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.entity + Defines.API_ENDPOINTS.DELETE;
    let obs: Observable<any> = this.http.post<ApiResponse<string>>
      (apiUrl, new ApiRequest(entityId, ''), this.httpOptions);

    return obs;

  }



  public listGroup(): Observable<ApiResponse<Array<GroupModel>>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.LIST;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<GroupModel>>>
      (apiUrl, new ApiRequest(null, ""), this.httpOptions).pipe(
        tap(list_ => console.log('get List group: ' + list_)),
        catchError(this.handleError<any>('Error'))
      );
    return obs;
  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };

  }


  public setmodelTobeEdit(entity: EntityModel) {
    debugger;
    this.modeltoEdit = entity;
    this.mode = 1;
  }


}
