import { Component, OnInit } from '@angular/core';
import { AccountService } from '../account.service';
import { AdminViewUser } from '../../shared/models/admin-view-user-model';
import { Defines } from '../../shared/static/defines';
import { Router } from '@angular/router';

@Component({
  selector: 'ndp-signup-requests',
  templateUrl: './signup-requests.component.html',
  styleUrls: ['./signup-requests.component.css']
})
export class SignupRequestsComponent implements OnInit {

  usersAccounts = new Array<AdminViewUser>();
  filteredUsersAccounts: Array<AdminViewUser>;
  inialized = false;
  expandedUserId: number;
  dropDownUserId: number;
  dropDown: boolean;

  rejectedUserId: number;
  rejectedUser: AdminViewUser;
  editRejectedUser: boolean;

  showDetails: boolean = true;
  saveButtonClicked: boolean;

  //Pagination Filtering & Sorting
  p: number = 1;
  perPage: number = 4;
  //filtering
  filter: string;
  //================ Authorization ================
  authorizationUrl: string;
  defines = Defines.API_CRUD;

  //lk signup status
  pending = Defines.SIGNUP_STATUS.PENDING;

  constructor(private accountService: AccountService, private router: Router) { }

  ngOnInit() {
    this.authorizationUrl = this.accountService.authorizationUrl;
    this.filteredUsersAccounts = this.usersAccounts;
    this.getSignUpRequests();
  }

  getSignUpRequests() {
    this.accountService.getSignUpRequests().subscribe(res => {
      this.usersAccounts = res.responsePayload;
      this.inialized = true;
    });
  }

  setExpandedUserId(event: Event, user: AdminViewUser) {
    event.stopPropagation();
    this.dropDownClose();
    if (this.expandedUserId !== user.id) {
      this.expandedUserId = user.id;
      this.rejectedUserId = user.id;
      this.rejectedUser = JSON.parse(JSON.stringify(user));
      this.showDetails = true;
    }
  }
  closeExpandedUserId() {
    this.expandedUserId = null;
    this.showDetails = false;
  }
  dropDownMenu(event: Event, user: AdminViewUser) {
    event.stopPropagation();
    if (!this.dropDown || this.dropDownUserId != user.id) {
      this.dropDownUserId = user.id;
      this.dropDown = true;
    } else {
      this.dropDown = false;
    }
  }

  approveUser(user: AdminViewUser) {
    this.accountService.isSignupApproval = true;
    //this.router.navigate(["admin/addeditaccount"], { queryParams: { userId: user.id, approve: 1 } });
    this.router.navigateByUrl('user/approve/' + user.id);
  }

  rejectUser(user: AdminViewUser) {
    //close details section
    //debugger;
    this.expandedUserId = user.id;
    this.showDetails = true;

    this.rejectedUserId = user.id;
    this.rejectedUser = JSON.parse(JSON.stringify(user));
    this.editRejectedUser = true;

    this.dropDownClose();
  }

  dropDownClose() {
    this.dropDownUserId = null;
    this.dropDown = false;
  }

  saveRejectedUser(user: AdminViewUser) {
    if (!user.rejectionReason) {
      this.saveButtonClicked = true;
    }
    else {
      user.signupStatus = Defines.SIGNUP_STATUS.REJECTED;

      this.accountService.approveUser(user).subscribe(res => {
        if (res.errors.length > 0 || res.status != 0) {
          user.signupStatus = Defines.SIGNUP_STATUS.PENDING;
        } else {
          this.getSignUpRequests();
          this.rejectedUserId = null;
          this.editRejectedUser = false;
        }
      });
    }
  }

  cancel() {
    this.rejectedUserId = null;
    this.editRejectedUser = false;
    this.saveButtonClicked = false;
  }
  closeAllMenus() {
    this.closeExpandedUserId();
    this.dropDownClose();
  }
  setFilteredUsersAccounts(filtered: AdminViewUser[]) {
    this.filteredUsersAccounts = filtered;
  }
}
