import { Pipe, PipeTransform } from '@angular/core';
import { CampaignModel } from '../models/campaign-model';

@Pipe({
    name: 'entityFilter'
})
export class CampaignEntityFilter implements PipeTransform {
    transform(campaigns: CampaignModel[], entity: String): any {
        const filtered = [];
        if (campaigns != null) {
            if (!entity) {
                return campaigns;
            } else {
                for (let i = 0; i < campaigns.length; i++) {
                    const c = campaigns[i];
                    if (c.entityName === entity) {
                        filtered.push(c);
                    }
                }
                return filtered;
            }
        }
    }
}
