import { AddEditCampaignComponent } from '../campaigns/add-edit-campaign/add-edit-campaign.component';
import { NotAuthorizedComponent } from '../general/not-authorized/not-authorized.component';
import { AddEditGroupComponent } from '../group/add-edit-group/add-edit-group.component';
import { GroupModule } from '../group/group.module';
import { AccountListComponent } from '../account/list/list.component';
import { AccountEditComponent } from '../account/add-edit/add-edit.component';
import { EntityListComponent } from '../entity/entity-list/entity-list.component';
import { ChangePasswordComponent } from '../account/change-password/change-password.component';
import { UsersAccountsListComponent } from '../account/users-accounts-list/users-accounts-list.component';
import { EntityAddEditComponent } from '../entity/entity-add-edit/entity-add-edit.component';
import { RegisterComponent } from '../sign-up/register/register.component';
import { AddEditUsersAccountsComponent } from '../account/add-edit-users-accounts/add-edit-users-accounts.component';
import { ForgetComponent } from '../login/forget-password/forget-password.component';
import { ResetComponent } from '../login/reset-password/reset-password.component';
import { RoleListComponent } from '../roles/role-list/role-list.component';
import { SignupRequestsComponent } from '../account/signup-requests/signup-requests.component';
import { SmscListComponent } from '../smsc/smsc-list/smsc-list.component';
import { AddEditSmscComponent } from '../smsc/add-edit-smsc/add-edit-smsc.component';
// import { RoleManagerComponent } from '../roles/role-manager/role-manager.component';
import { AppProfilesListComponent } from '../app-profiles/app-profiles-list/app-profiles-list.component';
import { AppProfilesAddEditComponent } from '../app-profiles/app-profiles-add-edit/app-profiles-add-edit.component';
import { ReportPageComponent } from '../reports/report-page/report-page.component';
import { AddEditComponent } from '../roles/add-edit/add-edit.component';
import { AddEditRoleComponent } from '../roles/add-edit-role/add-edit-role.component';
import { ResetExpiryPassComponent } from '../login/reset-expiry-password/reset-expiry-password.component';


const appRoutes: Route[] = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'home', component: HomePageComponent },
  { path: 'campaigns', component: CampaignListComponent },
  { path: 'campaigns/create', component: AddEditCampaignComponent },
  { path: 'campaigns/edit/:id', component: AddEditCampaignComponent },
  { path: 'campaigns/clone/:id', component: AddEditCampaignComponent },
  { path: 'smstemplates', component: SmsTemplateListComponent },
  { path: 'groups', component: GroupListComponent },
  { path: 'groups/get/:id', component: AddEditGroupComponent },
  { path: 'groups/create', component: AddEditGroupComponent },
  { path: 'roles', component: RoleListComponent },
  { path: 'roles/create', component: AddEditRoleComponent },
  { path: 'roles/edit/:id', component: AddEditRoleComponent },
  { path: 'notauthorized', component: NotAuthorizedComponent },
  { path: 'myprofile/view', component: AccountListComponent },
  { path: 'myprofile/edit', component: AccountEditComponent },
  { path: 'entity', component: EntityListComponent },
  { path: 'entity/add', component: EntityAddEditComponent },
  { path: 'user/list', component: UsersAccountsListComponent },
  { path: 'account/change-password', component: ChangePasswordComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'user/edit/:id', component: AddEditUsersAccountsComponent },
  { path: 'user/approve/:id', component: AddEditUsersAccountsComponent },
  { path: 'user/add', component: AddEditUsersAccountsComponent },
  { path: 'account/change-password', component: ChangePasswordComponent },
  { path: 'forget-password', component: ForgetComponent },
  { path: 'reset-password/:code/:email', component: ResetComponent },
  { path: 'reset-expiry-password/:email', component: ResetExpiryPassComponent },
  { path: 'requests', component: SignupRequestsComponent },
  { path: 'appProfile', component: AppProfilesListComponent },
  { path: 'addEditAppProfile', component: AppProfilesAddEditComponent },
  { path: 'smsc', component: SmscListComponent },
  { path: 'smsc/get/:id', component: AddEditSmscComponent },
  { path: 'smsc/create', component: AddEditSmscComponent },
  { path: 'reports', component: ReportPageComponent }]
// const extraOptions: ExtraOptions = { "useHash": true };
const extraOptions: ExtraOptions = { enableTracing: false, onSameUrlNavigation: 'reload', useHash: true };
@NgModule({
  declarations: [],
  imports: [
    CommonModule, RouterModule.forRoot(appRoutes, extraOptions), GroupModule
  ], exports: [RouterModule]
})
export class NDPRoutingModule { }
