import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { EntityServiceService } from '../entity-service.service';
import { AppProfileModel } from 'src/app/shared/models/app-profile-model';
import { EntityModel } from 'src/app/shared/models/entity-model';
import { SelectItem } from 'primeng/api';
import { Router } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { FakeSenderModel } from 'src/app/shared/models/fake-sender-model';
import { LkGroup } from 'src/app/shared/models/lk-group';
import { User } from 'src/app/shared/models/user-model';
import { Defines } from 'src/app/shared/static/defines';

@Component({
  selector: 'ndp-entity-add-edit',
  templateUrl: './entity-add-edit.component.html',
  styleUrls: ['./entity-add-edit.component.css'],
})
export class EntityAddEditComponent implements OnInit {
  groupsSlectItem: SelectItem[];
  selectedGroups = [];
  modelToAdd: EntityModel = new EntityModel();
  submitted = false;
  selectedrEntityType = "2";
  selectedProfile = 1;
  mainForm: FormGroup;
  balanceForm: FormGroup;
  modelToEdit = new EntityModel();
  fakeSenderArray = new Array<FakeSenderModel>();
  fakesenderDuplicated = false;
  fakesenderEmpty = false;
  public mode = 0;
  profilemap = new Map();
  dailyQuotaValid = true;
  MaxThroughputValid = true;
  reportDeliveryValid = true;
  canAddFakeSenderValid = true;
  initialBalance: number;
  allAppProfiles = new Array<AppProfileModel>();
  profileItems = new Array<SelectItem>();
  public user: User;

  entityType: SelectItem[];
  constructor(private entityService: EntityServiceService, private route: Router, private formBuilder: FormBuilder) { }
  get f() { return this.mainForm.controls; }
  get getBalanceControlls() { return this.balanceForm.controls; }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    this.mainForm = this.formBuilder.group({
      entityName: ['', Validators.required],
      entityType: new FormControl(),
      dailyQuota: ['', [Validators.required, Validators.min(1)]],
      appProfile: ['', [Validators.required, Validators.min(1)]],
      maxThroughput: ['', [Validators.required, Validators.min(1)]],
      deliveryReport: new FormControl(),

    });

    this.balanceForm = this.formBuilder.group({
      isLimited: new FormControl(),
      balance: ['', [Validators.required, Validators.min(this.modelToAdd.balance)]],
      balanceToAdd: [0, [Validators.required]]
    });
    this.groupsSlectItem = [];
    this.entityService.listGroup().subscribe(resp => {

      let groupListAllAuthorized = this.entityService.accessGranted(Defines.PRIVILEGES.GROUP.LIST, Defines.ACCESS_LEVELS.EVERYTHING);
      console.log("groupListAllAuthorized", groupListAllAuthorized);
      resp.responsePayload.forEach(group => {

        if (groupListAllAuthorized || (group.ownerEntity !== this.modelToEdit.entityId && group.ownerEntity == this.user.entityId)) {
          this.groupsSlectItem.push({ label: group.groupName, value: group.groupId });
        }
      });
      this.groupsSlectItem = JSON.parse(JSON.stringify(this.groupsSlectItem));
    });

    this.entityType = [
      { label: 'External', value: '2' },
      { label: 'Internal', value: '1' }

    ];
    this.mode = this.entityService.mode;

    this.entityService.getAllAppProfile().subscribe(res => {

      this.allAppProfiles = res.responsePayload;
      this.profileItems.push({ label: "select app profile ", value: -1 });
      this.allAppProfiles.forEach(profile => {
        this.profileItems.push({ label: profile.appProfileName, value: profile.appProfileId });
        this.profilemap.set(profile.appProfileId, profile);
      })

      this.profileItems = JSON.parse(JSON.stringify(this.profileItems));


    });

    // let editModel = <EntityModel>this.entityService.popSessionObject(Defines.SESSION_KEYS.entityToEdit);
    if (this.mode == 1) {
      this.modelToAdd = this.entityService.modeltoEdit;//TODO ==> make this get from service live ( Service returns lot of unset fields (problem in DAO))

      this.modelToAdd.balanceToAdd = 0;
      this.selectedrEntityType = this.modelToAdd.isInternal ? "1" : "2";
      this.fakeSenderArray = this.modelToAdd.fakeSendrName;
      if (this.modelToAdd.groupList.length > 0)
        this.modelToAdd.groupList.forEach(element => {
          this.selectedGroups.push(element.groupId);
        });
    }
  }

  saveEntity() {
    this.submitted = true;
    if (this.mainForm.invalid) {
      return;
    }
    if (this.fakesenderDuplicated)
      return;
    if (this.modelToAdd.isLimited != undefined && this.modelToAdd.isLimited && this.balanceForm.invalid) {
      return;
    }
    if (this.modelToAdd.dailyQuota > this.profilemap.get(this.modelToAdd.appProfileId).maximumDailyQuota) {
      this.dailyQuotaValid = false;
      return;
    }

    if (this.modelToAdd.maxThroughput > this.profilemap.get(this.modelToAdd.appProfileId).maxThroughput) {
      this.MaxThroughputValid = false;
      return;
    }
    console.log("console : ", this.selectedGroups);

    this.modelToAdd.groupList = [];

    // this.modelToAdd.groupList
    this.selectedGroups.forEach(element => {
      let tempgroup = new LkGroup("", element);
      this.modelToAdd.groupList.push(tempgroup);
    });
    this.modelToAdd.fakeSendrName = this.fakeSenderArray;

    this.modelToAdd.isInternal = this.selectedrEntityType == "1";
    if (this.modelToAdd.active == undefined)
      this.modelToAdd.active = false;

    if (this.modelToAdd.isLimited == undefined)
      this.modelToAdd.isLimited = false;

    if (this.modelToAdd.deliveryReport == undefined)
      this.modelToAdd.deliveryReport = false;

    if (this.mode == 0) {
      this.entityService.addEntity(this.modelToAdd).subscribe(resp => {

        //debugger;
        console.log("response ", resp.responsePayload);
        if (resp.status == 0) {
          this.modelToAdd = new EntityModel();

          this.route.navigate(['/entity']);
        }
      });
    } else if (this.mode == 1) {
      if (this.initialBalance != this.modelToAdd.balance) {
        //this.balanceToAdd
      }
      this.entityService.editEntity(this.modelToAdd).subscribe(resp => {

        //debugger;
        if (resp.status == 0) {
          this.route.navigate(['/entity']);
        }
        console.log("response ", resp.responsePayload);
        // this.outputEntity.emit(this.modelToAdd);

      });
    }


  }

  cancel() {
    if (this.mode == 0) {
      this.modelToAdd = new EntityModel();
      this.entityService.mode = 0;
      this.route.navigate(['/entity']);
    } else {
      this.modelToAdd = this.entityService.modeltoEdit;
      this.entityService.mode = 0;
      this.route.navigate(['/entity']);
      // this.outputEntity.emit(null);
    }
  }

  addfakeSender() {
    if (this.fakeSenderArray.length == 0) {

      let temp = new FakeSenderModel();
      temp.isDefault = true;
      this.fakeSenderArray.push(temp);
    }
    else {
      this.fakeSenderArray.push(new FakeSenderModel());
    }
  }

  removeFakeSender() {
    this.fakeSenderArray.splice(this.fakeSenderArray.length - 1, 1);
  }

  removeSpecificSender(n: number) {
    if (this.fakeSenderArray[n].isDefault) {
      if (n - 1 > -1)
        this.fakeSenderArray[n - 1].isDefault = true;
      else if (n + 1 < this.fakeSenderArray.length)
        this.fakeSenderArray[n + 1].isDefault = true;
    }
    this.fakeSenderArray.splice(n, 1);
  }

  checkFakeSenderDuplication(fakesender) {
    let count = 0;
    console.log("fakesender : ", fakesender);
    if (fakesender != undefined) {

      this.fakeSenderArray.forEach(element => {
        if (fakesender.toLowerCase().trim() == element.fakeSenderName.toLowerCase().trim()) {
          count++;
        }
      });

      if (count > 1) {
        console.log(fakesender, "duplicated ");
        this.fakesenderDuplicated = true;
      } else {
        this.fakesenderDuplicated = false;
      }
    }
  }

  checkvalues() {
    if (this.modelToAdd.appProfileId > 0) {
      if (this.modelToAdd.dailyQuota > this.profilemap.get(this.modelToAdd.appProfileId).maximumDailyQuota) {
        this.dailyQuotaValid = false;

      } else {
        this.dailyQuotaValid = true;
      }


      if (this.modelToAdd.maxThroughput > this.profilemap.get(this.modelToAdd.appProfileId).maxThroughput) {
        this.MaxThroughputValid = false;

      } else {
        this.MaxThroughputValid = true;

      }

      if (this.profilemap.get(this.modelToAdd.appProfileId).requiresDeliveryReport) {
        this.reportDeliveryValid = true;
      } else {
        this.reportDeliveryValid = false;
        this.modelToAdd.deliveryReport = false;
      }

    }
  }
}

