  show() {
    if (this.campaignForm.get('notificationText').value === null || this.campaignForm.get('notificationText').value === '') {
      this.messageService.addWarnings(['Please fill in the SMS details first']);
      this.moveToSMS();
      return;
    }
    const ref = this.dialogService.open(TestSmsComponent, {
      width: '40%',
      showHeader: false,
      data: {
        msg: this.campaignForm.get('notificationText').value
      }
    });

    ref.onClose.subscribe((s: string) => {
      if (s) {
        const model = new TestSmsModel();
        model.admCampaignsModel = this.campaignForm.value;
        const arabic = /[\u0600-\u06FF]/;
        if (arabic.test(model.admCampaignsModel.notificationText)) {
          model.admCampaignsModel.language = 'AR';
        }
        model.dialWithParameters = s;
        this.campaignService.testSms(model).toPromise().then((r) => {
          if (r.status === 0) {
            this.messageService.addInfo('Sent Test SMS to ' + s.split(',')[0]);
          } else {
            this.messageService.addWarnings(['Sending SMS Failed']);
          }
        });
      }
    });
  }

  showSmsScriptWarning() {
    if (this.campaignToEdit && this.mode === 1) {
      this.messageService.addWarnings(['Placeholders entered while editing might not match previously uploaded file or group parameters.']);
    }
    return;
  }

