import { Injectable, OnInit } from '@angular/core';
import { BaseService } from '../shared/services/base-service';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { Defines } from '../shared/static/defines';
import { ApiRequest } from '../shared/models/api-request';
import { GroupModel } from '../shared/models/group-model';
import { catchError, map, tap } from 'rxjs/operators';
import { GroupMsisdnModel } from '../shared/models/group-msisdn-model';
import { ExportedDialModel } from '../shared/models/export-dials-model';

@Injectable({
    providedIn: 'root'
})
export class GroupService extends BaseService {
    constructor(private client: HttpClient) {
        super(client);
    }

    initAuthorizationUrl() {
        this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.GROUPS;
        console.log("Authorization Url =>", this.authorizationUrl);
    }
    public create(model: GroupModel): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.CREATE;
        console.log("model before create is => ", model);
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).pipe(
                tap(group => console.log('create group: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public update(model: GroupModel): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.UPDATE;
        console.log("model before update is => ", model);
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).pipe(
                tap(group => console.log('update group: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }


    public get(id: number): Observable<ApiResponse<GroupModel>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.GET;
        let obs: Observable<any> = this.http.post<ApiResponse<GroupModel>>
            (apiUrl, new ApiRequest(id, ""), this.httpOptions).pipe(
                tap(group => console.log('get group by id: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public delete(id: number): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.DELETE;
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(id, ""), this.httpOptions).pipe(
                tap(group => console.log('delete group by id: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public list(): Observable<ApiResponse<Array<GroupModel>>> {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.LIST;
        let obs: Observable<any> = this.http.post<ApiResponse<Array<GroupModel>>>
            (apiUrl, new ApiRequest(null, ""), this.httpOptions).pipe(
                tap(list_ => console.log('get List group: ' + list_)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public listGroupPromise() {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS + Defines.API_CRUD.LIST;
        return this.http.post<ApiResponse<Array<GroupModel>>>
            (apiUrl, new ApiRequest(null, ""), this.httpOptions).toPromise();
    }

    public searchDials(model: GroupMsisdnModel): Observable<ApiResponse<Array<GroupMsisdnModel>>> {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS
            + Defines.API_CRUD.SEARCH_DIALS;
        let obs: Observable<any> = this.http.post<ApiResponse<Array<GroupMsisdnModel>>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).pipe(
                tap(list_ => console.log('get List dials: ' + list_)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public getAllDials(model: number) {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS
            + Defines.API_CRUD.LIST_DIALS;
        return this.http.post<ApiResponse<Array<ExportedDialModel>>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).toPromise();
    }

    public getAllFailedDials(model: number) {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS
            + Defines.API_CRUD.FAILED_DIALS;
        return this.http.post<ApiResponse<Array<string>>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).toPromise();
    }


    public deleteDial(model: GroupMsisdnModel): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.GROUPS
            + Defines.API_CRUD.DELETE_DIALS;
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).pipe(
                tap(list_ => console.log('delete dial: ' + list_)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {

            // TODO: send the error to remote logging infrastructure
            console.error(error); // log to console instead

            // TODO: better job of transforming error for user consumption
            console.log(`${operation} failed: ${error.message}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };

    }

}
