import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { Location } from '@angular/common';
import { SmscAccountModel } from 'src/app/shared/models/smsc-account-model';
import { SmscService } from '../smsc.service';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { AppProfileModel } from 'src/app/shared/models/app-profile-model';
import { AppProfilesService } from 'src/app/app-profiles/app-profiles.service';
import { SelectItem } from 'primeng/api';


@Component({
  selector: 'ndp-add-edit-group',
  templateUrl: './add-edit-smsc.component.html',
  styleUrls: ['./add-edit-smsc.component.css']
})
export class AddEditSmscComponent implements OnInit {

  allAppProfiles: Array<AppProfileModel>;
  profileItems = new Array<SelectItem>();
  [x: string]: any;
  public smscModel: SmscAccountModel;
  public updateResponse: number;
  public isCreate: boolean = true;
  mainForm: FormGroup;
  submitted = false;



  constructor(private smscService: SmscService,
    private appProfilesService: AppProfilesService,
    private route: ActivatedRoute,
    private router: Router,
    private location: Location,
    private formBuilder: FormBuilder) { }

  get f() { return this.mainForm.controls; }
  ngOnInit() {

    this.appProfilesService.list().subscribe(res => {
      
      console.log("App profile => ", this.appProfilesList);
      this.allAppProfiles = res.responsePayload;
      this.profileItems.push({label: "select app profile ", value: -1});
      this.allAppProfiles.forEach(profile => {
        this.profileItems.push({ label: profile.appProfileName, value: profile.appProfileId });
        //debugger;
      })

      this.profileItems = JSON.parse(JSON.stringify(this.profileItems));

    })





    this.mainForm = this.formBuilder.group({
      smscName: ['', [Validators.required,Validators.maxLength(25)]],
      smscIp: ['', Validators.required],
      smscPort: ['', [Validators.required, Validators.min(0)]],
      smscUsername: ['', [Validators.required,Validators.maxLength(25)]],
      smscPassword: ['', Validators.required],
      appProfile: ['', Validators.required]
    });

    if (this.route.snapshot.routeConfig.path == "smsc/get/:id") {
      const id = +this.route.snapshot.paramMap.get('id');
      this.get(id);
      this.isCreate = false;
    } else {
      this.smscModel = new SmscAccountModel;
    }
  }

  get(id: number): void {
    this.smscService.get(id)
      .subscribe(response => this.smscModel = response.responsePayload);
  }

  save(): void {
    this.smscService.scrollIfFormHasErrors(this.mainForm);
    this.submitted = true;
    if (this.mainForm.invalid) {
      return;
    }
    if (!this.isCreate) {
      this.update();
    } else {
      this.create();
    }
  }

  create(): void {
    this.smscService.create(this.smscModel)
      .subscribe(response => {
        if (response.status === 0) {
          this.router.navigate(["smsc"]);
        }
      });
  }

  update(): void {
    this.smscService.update(this.smscModel)
      .subscribe(response => {
        if (response.status === 0) {
          this.router.navigate(["smsc"]);
        }
      }
      );
  }

  cancel() {
    this.location.back();
  }
}
