import { Component, OnInit } from '@angular/core';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { Router } from '@angular/router';
import { SmscAccountModel } from 'src/app/shared/models/smsc-account-model';
import { SmscService } from '../smsc.service';
import { Defines } from 'src/app/shared/static/defines';

@Component({
  selector: 'ndp-smsc-list',
  templateUrl: './smsc-list.component.html',
  styleUrls: ['./smsc-list.component.css']
})
export class SmscListComponent implements OnInit {
  smscList: SmscAccountModel[];
  filteredSmscList = null;
  selectedId: number;
  inialized = false;
  dropDown: boolean;
  dropDownClick: number;
  dropDownId: number;
  /*=============================*/
  /* Pagination Filtering & Sorting*/
  p = 1;
  perPage = 4;
  /*FILTERING*/
  filter: string;
  /*=============================*/

  /*SORTING*/
  key = 'name';
  reverse = false;

  //Authorization Vars
  authorizationUrl: string;
  defines = Defines.API_CRUD;

  constructor(private messageService: NdpMessageService,
    private smscService: SmscService,
    private router: Router) { }

  ngOnInit() {
    this.authorizationUrl = this.smscService.authorizationUrl;
    this.smscList = new Array<SmscAccountModel>();
    this.filteredSmscList = this.smscList;
    this.list();
  }

  dropDownOpen(smsc: SmscAccountModel) {
    this.selectedId = smsc.accountId;
    this.dropDownId = this.selectedId;
    this.dropDownClick = 0;
    this.dropDown = true;
  }
  dropDownClose() {
    this.selectedId = null;
    this.dropDown = false;
  }

  hideDropDown() {
    this.dropDownClick++;
    if (this.dropDown && this.dropDownClick > 1) {
      this.dropDown = false;
      this.dropDownId = null;
    }
  }

  list(): void {
    this.smscService.list().subscribe(response => {
      this.smscList = response.responsePayload;
      this.inialized = true;
      console.log('Retrieved Accounts ', response.responsePayload);
    });
  }

  update() {
    this.router.navigateByUrl('/smsc/get/' + this.selectedId);
  }

  create() {
    this.router.navigateByUrl('/smsc/create');
  }

  delete(smsc: SmscAccountModel): void {
    this.smscService.delete(smsc.accountId).subscribe(response => {
      this.smscList = this.smscList.filter(x => x.accountId !== smsc.accountId);
      this.filteredSmscList = this.filteredSmscList.filter(x => x.accountId !== smsc.accountId);
    });
    this.dropDownClose();
  }
  setFilteredSmscList(filtered: SmscAccountModel[]) {
    this.filteredSmscList = filtered;
  }
}
