import { Component, OnInit } from '@angular/core';
import { RoleManagerService } from '../role-manager.service';
import { PrivilegeModel } from 'src/app/shared/models/privilege-model';
import { RoleModel } from 'src/app/shared/models/role-model';
import { RolePrivilegeModel } from 'src/app/shared/models/role-privilege-model';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'ndp-add-edit-role',
  templateUrl: './add-edit-role.component.html',
  styleUrls: ['./add-edit-role.component.css']
})
export class AddEditRoleComponent implements OnInit {
  allRolesList = new Array<RoleModel>();
  role: RoleModel;
  privilegesList: Array<PrivilegeModel>;
  found = false;
  invalidLength = false;
  /*SORTING*/
  key = 'label';
  reverse = false;
  /*=============================*/
  mode = 0;
  descriptionError:string="";
  nameError:string="";
  roleForm = new FormGroup({
    roleName: new FormControl('', [Validators.required, Validators.maxLength(25)]),
    roleDescription: new FormControl('', [Validators.required, Validators.maxLength(25)])
  });
  constructor(private rolesService: RoleManagerService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.role = new RoleModel();
    this.role.isEditable = true;
    this.role.privilegeList = new Array<RolePrivilegeModel>();

    // Load All Available Privileges
    this.loadPrivileges();
    console.log('Privilage List Before Roles', this.role.privilegeList);
    console.log('getting roles');
    this.rolesService.getAllRoles().subscribe(roles => {
      this.allRolesList = roles.responsePayload;
    });
    console.log('got roles', this.allRolesList);
    console.log('Privilage List After Roles', this.role.privilegeList);
    this.route.paramMap.subscribe(params => {
      const roleId = +params.get('id');
      console.log('url role ' + roleId);
      if (roleId) {
        this.mode = 1;
        this.getRole(roleId);
      } else {
        this.mode = 0;
      }
    });
  }
  roleCheck(s: string) {
    console.log('change ' + s);
    this.found = false;
    this.allRolesList.forEach(r => {
      if (r.label === s) {
        this.found = true;
      }
    });
  }

  validateLength(s: string, l: number) {
    if(l==25){
    if (s.length > l)
       this.nameError="Role Name must not exceed "+l+" characters";
       else 
       this.nameError="";
  }else{
    if (s.length > l)
       this.descriptionError="Role Description must not exceed "+l+" characters";
       else 
       this.descriptionError="";
  }
  }

  getRole(roleId: number) {
    this.rolesService.getRole(roleId).subscribe(response => {
      this.role = new RoleModel();
      this.role = response.responsePayload;
      console.log('got role ', this.role);
    });
  }

  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }

  loadPrivileges() {
    this.rolesService.getAllPrivilege().subscribe(response => {
      this.privilegesList = new Array<PrivilegeModel>();
      response.responsePayload.forEach(priv => {
        if (priv.isDefault) {
          const rolePriv = new RolePrivilegeModel(null, priv.id, 3);
          this.role.privilegeList.push(rolePriv);
          if (priv.childList) {
            priv.childList.forEach(child => {
              const p = new RolePrivilegeModel(null, child.id, 3);
              this.role.privilegeList.push(p);
            });
          }
        } else {
          this.privilegesList.push(priv);
        }
      });
      // this.privilegesList = response.responsePayload;
      console.log('new role is ', this.role.privilegeList);
    });
  }

  onSelectPrivilege(eventObject, pid: number) {
    if (this.role.privilegeList) {
      if (eventObject.target.checked) {
        const rolePrivilege = new RolePrivilegeModel(null, pid, 1);
        this.role.privilegeList.push(rolePrivilege);
      } else {
        const par = this.privilegesList.findIndex(p => p.id === pid);
        if (par !== -1) {
          this.privilegesList[par].childList.forEach(obj => {
            const child = this.role.privilegeList.findIndex(p => p.privilegeId === obj.id);
            if (child !== -1) {
              this.role.privilegeList.splice(child, 1);
            }
          });
        }
        const index = this.role.privilegeList.findIndex(p => p.privilegeId === pid);
        this.role.privilegeList.splice(index, 1);
      }
      console.log('Current Privilage List', this.role.privilegeList);
    }
  }

  checkPrivilegeActive(pid: number) {
    if (this.role.privilegeList) {
      if (this.role.privilegeList.findIndex(p => p.privilegeId === pid) !== -1) {
        return true;
      }
    }
    return false;
  }

  getPrivilageChildren(p: PrivilegeModel) {
    return p.childList;
  }

  getPrivilageLabel(s: string) {
    return s;
    // return s.substring(0, s.indexOf(' '));
  }

  setAccessLevel(n: number, p: PrivilegeModel) {
    if (this.role.privilegeList) {
      console.log('P to set ', p);
      const index = this.role.privilegeList.findIndex(i => i.privilegeId === p.id);
      console.log('index ', index);
      this.role.privilegeList[index].accessLevel = n;
    }
  }

  getPrivilegeCheck(p: PrivilegeModel) {
    if (this.role.privilegeList) {
      const index = this.role.privilegeList.findIndex(i => i.privilegeId === p.id);
      return (index !== -1);
    }
  }
  getAccessLevelRadio(n: number, p: PrivilegeModel) {
    if (this.role.privilegeList) {
      const index = this.role.privilegeList.findIndex(i => i.privilegeId === p.id);
      return this.role.privilegeList[index].accessLevel === n;
    }
  }

  onClickSave() {

    if (this.found) {
      return;
    }
    if (this.role.privilegeList.length > 0) {
      if (this.mode === 0) {
        this.rolesService.addRole(this.role).subscribe(response => {
          console.log('Creating Role ', response.responsePayload);
          this.navigateBack();
        });
      } else {
        this.rolesService.editRole(this.role).subscribe(response => {
          console.log('Updating Role ', response.responsePayload);
          this.navigateBack();
        });
      }

    } else {
      console.log('Select at least 1 privilage');
      alert('Select at least 1 privilage');
    }
  }

  navigateBack() {
    this.router.navigate(['roles']);
  }

  onClickCancel() {
    this.navigateBack();
  }
}
