import { Component, OnInit } from '@angular/core';
import { DynamicDialogRef } from 'primeng/api';
import { DynamicDialogConfig } from 'primeng/api';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';

@Component({
  selector: 'ndp-test-sms',
  templateUrl: './test-sms.component.html',
  styleUrls: ['./test-sms.component.css']
})
export class TestSmsComponent implements OnInit {

  constructor(public ref: DynamicDialogRef, public config: DynamicDialogConfig, private formBuilder: FormBuilder) { }
  smsTestForm: FormGroup;
  smsText: string;
  public invalidPlaceholders;
  ngOnInit() {
    // msg: this.config.msg;
    this.smsText = this.config.data.msg;

    this.smsTestForm = this.formBuilder.group({
      dialWithParameters: ['', [Validators.required, Validators.pattern('[+]?[0][1][1|0|2|5][0-9]{8}([,][A-z0-9]+[=][\u0001-\u002B\u002D-\u06FF]+)*')]]
    });
  }
  get fc() {
    return this.smsTestForm.controls;
  }
  send() {
    console.log('sending test sms: ', this.smsTestForm.get('dialWithParameters').value);
    const placeholders = this.populatePlaceHolders(this.smsTestForm.controls['dialWithParameters'].value);
    this.invalidPlaceholders = this.checkPlaceholdersExist(this.smsText,placeholders,this.smsTestForm.get('dialWithParameters'));
    
    for (const i in this.smsTestForm.controls) {
      if (this.smsTestForm.controls[i]) { this.smsTestForm.controls[i].markAsTouched(); }
    }

    if (this.smsTestForm.invalid) {
      console.log('form is invalid =>', this.smsTestForm);
      return;
    }
    this.ref.close(this.smsTestForm.get('dialWithParameters').value);
  }
  close() {
    console.log('closing');
    this.ref.close();
  }

  populatePlaceHolders(dial:string) : any {
    let placeholders = {};
    let line: string  = dial + '\n';

    let params = line.substring(line.indexOf(',') + 1);

    do {
      placeholders['{' + params.substring(0, params.indexOf('=')) + '}'] = true;
      params = params.substring((params.indexOf(',') > 0) ? params.indexOf(',') + 1 : params.indexOf('\n'));
    } while (params.indexOf('\n') > 0);
    
    console.log('placeholders =>',placeholders);
    return placeholders;
  }

  checkPlaceholdersExist(smsScript: string, placeholdersMap:any, formControl:AbstractControl):string {
    let invalidPlaceholders = "[";
    let remainingSmsScript = smsScript == null ? "" : smsScript;
    let placeholder;
    let hasErrors: boolean = false;
    while (placeholder = remainingSmsScript.match("\{[A-Za-z0-9]+\}")) {
      if (placeholdersMap[placeholder]) {
        console.log(placeholder+" hwhw");
      } else {
        hasErrors = true;
        if(!invalidPlaceholders.match(placeholder)){
          invalidPlaceholders = invalidPlaceholders + placeholder + " ";
        }
      }
      remainingSmsScript = remainingSmsScript.replace(placeholder, "");
    }
    let errors = formControl.errors;

    if (hasErrors) {
      if (errors) {
        errors['invalidPlaceholder'] = true;
      } else {
        errors = { invalidPlaceholder: true };
      }
    } else {
      if (errors && errors['invalidPlaceholder']) {
        delete errors['invalidPlaceholder'];
        if (Object.keys(errors).length === 0) {
          errors = null;
        }
      }
    }
    formControl.setErrors(errors);
    if(hasErrors){
      invalidPlaceholders = invalidPlaceholders.substr(0, invalidPlaceholders.length - 1) + "]";
    }else{
      invalidPlaceholders = "";
    }
    return invalidPlaceholders;
  }

}
