import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoginServiceService } from '../login-service.service';
import { Location } from '@angular/common';
import { ResetPasswordModel } from 'src/app/shared/models/reset-password-model';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { Defines } from 'src/app/shared/static/defines';

@Component({
    selector: 'ndp-expiry-reset',
    templateUrl: './reset-expiry-password.component.html',
    styleUrls: ['./reset-expiry-password.component.css']
})
export class ResetExpiryPassComponent implements OnInit {

    model: ResetPasswordModel = new ResetPasswordModel();
    confirmPassword: string;
    public statusResponse: string;

    constructor(private route: Router,
        private loginService: LoginServiceService,
        private activeRoute: ActivatedRoute,
        private messageService: NdpMessageService) { }

    ngOnInit() {
        this.model.email = this.activeRoute.snapshot.paramMap.get('email');
    }

    reset() {
        if(!this.model.password.match("^(?=.*[A-Za-z])(?=.*[0-9])[A-Za-z0-9!@#$%^&*(),.?\":{}|<>\\/]{8,}$")){
            this.messageService.addWarnings(["Password should be a minimum of 7 letters and 1 character"]);
            return;
        }
        if (this.confirmPassword === this.model.password) {
            this.loginService.resetExpiredPassword(this.model).subscribe(
                response => {
                    console.log("response =>", response);
                    if (response.status === Defines.API_RESPONSES.success) {
                        this.goToLogin();
                    } else {
                        this.statusResponse = response.statusMessage;
                    }
                }
            );
        } else {
            this.messageService.addWarnings(["Password Not Matched"]);
        }
    }

    goToLogin() {
        this.route.navigate(['login']);
    }

}