import { Component, OnInit, Input, Inject } from '@angular/core';
import { GroupModel } from 'src/app/shared/models/group-model';
import { ActivatedRoute, Router } from '@angular/router';
import { GroupService } from '../group.service';
import { UploadEvent, UploadFile, FileSystemFileEntry, FileSystemDirectoryEntry } from 'ngx-file-drop';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { FileModel } from 'src/app/shared/models/file-model';
import { Location } from '@angular/common';
import { FileTypeModel } from 'src/app/shared/models/file-type-model';
import { Defines } from 'src/app/shared/static/defines';
import { LookupsService } from 'src/app/shared/services/lookups.service';
import 'rxjs/add/operator/toPromise';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
import { User } from 'src/app/shared/models/user-model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Base64 } from 'js-base64';
@Component({
  selector: 'ndp-add-edit-group',
  templateUrl: './add-edit-group.component.html',
  styleUrls: ['./add-edit-group.component.css']
})

export class AddEditGroupComponent implements OnInit {
  public user: User;
  public group: GroupModel;
  public fileModel: FileModel = new FileModel;
  public files: UploadFile[] = [];
  public updateResponse: number;
  public fileUploaded: UploadFile;
  public fileTypes: FileTypeModel[] = [];
  public selectedFileType: number;
  public isCreate: boolean = true;
  public csvOptions = {
    fieldSeparator: ',',
    quoteStrings: '',
    decimalseparator: '.',
    showLabels: false,
    showTitle: false,
    useBom: true,
    noDownload: false,
  };

  submitted = false;
  profileForm = new FormGroup({
    groupName: new FormControl('', [Validators.required,Validators.maxLength(25)])
  });

  get pfc() { return this.profileForm.controls; }
  public sample0: any = [
    { number: '01112345679' },
    { number: '01112345678' },
    { number: '01112345677' },
    { number: '01112345676' },
    { number: '01112345675' }
  ];
  public sample1: any = [
    { number: '01112345679', name: 'Ahmed' },
    { number: '01112345678', name: 'Asmaa' },
    { number: '01112345677', name: 'Yasmine' },
    { number: '01112345676', name: 'Hossam' },
    { number: '01112345675', name: 'Alaa' }
  ];
  public sample2: any = [
    { number: '01112345679', name: 'Name=Ahmed', prize: 'Prize=100' },
    { number: '01112345678', name: 'Name=Asmaa', prize: 'Prize=200' },
    { number: '01112345677', name: 'Name=Yasmine', prize: 'Prize=300' },
    { number: '01112345676', name: 'Name=Hossam', prize: 'Prize=400' },
    { number: '01112345675', name: 'Name=Alaa', prize: 'Prize=500' }
  ];


  constructor(private route: ActivatedRoute, private routeLink: Router,
    private groupService: GroupService,
    private lookupsService: LookupsService,
    private messageService: NdpMessageService,
    private location: Location) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
    if (this.route.snapshot.routeConfig.path == "groups/get/:id") {
      const id = +this.route.snapshot.paramMap.get('id');
      this.getGroup(id);
      this.isCreate = false;
    } else {
      this.group = new GroupModel;
    }
    this.loadFileTypes();
  }

  activeFileType(id: number) {
    let isEditWithSameFileType: Boolean = false;
    if (!this.isCreate) {
      if (this.group.fileType === id) {
        isEditWithSameFileType = true;
      }
    }
    if (this.isCreate || isEditWithSameFileType) {

      this.fileTypes.forEach(fileType => {
        if (fileType.id === id) {
          fileType.isActive = true;
          this.fileModel.fileType = fileType.id;
        } else {
          fileType.isActive = false;
        }

      });
    }
  }


  loadFileTypes() {
    this.lookupsService.getfileTypes()
      .then((data: any) => this.setFileTypes(data.responsePayload));
  }

  setFileTypes(fileTypes: FileTypeModel[]) {
    this.fileTypes = fileTypes;
    this.activeFileType(0);
  }

  getGroup(id: number): void {
    this.groupService.get(id)
      .subscribe(response => {
        this.group = response.responsePayload;
        this.activeFileType(this.group.fileType);
      });
  }

  public fileOver(event) {
    console.log(event);
  }

  public fileLeave(event) {
    console.log(event);
  }

  public dropped(event: UploadEvent) {
    this.fileUploaded = event.files[0];
    if (event.files.length == 1) {
      if (this.fileUploaded.fileEntry.isFile) {
        if (!this.fileUploaded.relativePath.includes(".csv", 0)) {
          this.messageService.addWarnings(["Please Upload csv File!"]);
          this.fileUploaded = null;
          return;
        }
        const fileEntry = this.fileUploaded.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          if (file.size > 20 * 1024 * 1024) {
            this.messageService.addWarnings(["The file size should be <= 2 MB!"]);
            this.fileUploaded = null;
            return;
          }
          this.fileModel.fileName = file.name;
          let reader = this.createFileReader(file);
          reader.readAsText(file);
        });
      } else {
        const fileEntry = this.fileUploaded.fileEntry as FileSystemDirectoryEntry;
      }
    } else if (this.files.length > 1) {
      this.messageService.addWarnings(["Please Upload One File!"]);
    }
  }

  createFileReader(file): FileReader {
    let text: string | ArrayBuffer;
    let reader = new FileReader();
    reader.onloadend = (() => {
      text = reader.result;
      let line: string;
      if (text.toString().indexOf('\n') > 1) {
        line = text.toString().substr(0, text.toString().indexOf('\n'));
      } else {
        line = text.toString();
      }
      if (text.toString().trim() === '') {
        this.messageService.addWarnings(["File is empty!"]);
        this.fileUploaded = null;
        return null;
      } else if (!this.checkForFileType(line)) {
        this.messageService.addWarnings(["Uplouded File is not matched with selected type!"]);
        this.fileUploaded = null;
        return null;
      }
      let base64String = Base64.encode(text.toString());
      this.fileModel.b64String = base64String;
      this.messageService.addInfo("File uploaded successfully!");
    })
    return reader;
  }

  downloudSample() {
    this.fileTypes.forEach(fileType => {
      if (fileType.isActive === true) {
        if (fileType.id === 0) new AngularCsv(this.sample0, "Sample", this.csvOptions);
        else if (fileType.id === 1) new AngularCsv(this.sample1, "Sample", this.csvOptions);
        else new AngularCsv(this.sample2, "Sample", this.csvOptions);
      }
    });
  }

  save(): void {
    this.submitted = true;
    if (this.profileForm.valid) {
      if (this.fileUploaded !== undefined) {
        this.group.fileRequestModel = this.fileModel;
        if (!this.isCreate) {
          this.updateGroup();
        } else {
          this.createGroup();
        }
      } else {
        this.messageService.addError('File is mandatory!', '');
      }
    }
  }

  createGroup(): void {
    this.group.createdBy = this.user.id;
    this.group.ownerEntity = this.user.entityId;
    this.group.groupStatus = 1;
    this.group.dialsCount = 0;
    this.groupService.create(this.group)
      .subscribe(response => {
        console.log(response.responsePayload);
        if (response.status === 0) {
          this.routeLink.navigateByUrl('groups');
        }
      });
  }

  updateGroup(): void {
    this.group.groupStatus = 1;
    this.groupService.update(this.group)
      .subscribe(response => {
        console.log(response.responsePayload);
        if (response.status === 0) {
          this.routeLink.navigateByUrl('groups');
        }
      });
  }

  cancel() {
    this.routeLink.navigateByUrl("groups");
  }

  clearUploadedFile() {
    this.fileUploaded = undefined;
  }

  checkForFileType(line: string): boolean {
    console.log("line", line);
    console.log("selected file type", this.fileModel.fileType);
    let numberOfComma: number = line.split(",").length - 1;
    console.log("numberOfComma ", numberOfComma);
    if (this.fileModel.fileType === 0 && numberOfComma === 0) {
      return true;
    } else if (this.fileModel.fileType === 1 && numberOfComma === 1) {
      return true;
    } else if (this.fileModel.fileType === 2 && numberOfComma >= 2) {
      return true;
    } else {
      return false;
    }
  }
}
