import { Injectable } from "@angular/core";
import { BaseService } from "./base-service";
import { HttpClient } from "@angular/common/http";
import { ApiResponse } from "../models/api-response";
import { Defines } from "../static/defines";
import { ApiRequest } from "../models/api-request";
import { AuthorizationRequestModel } from "../models/authorization-request-model";


@Injectable({
    providedIn: 'root'
})
export class AuthorizationService extends BaseService {
    constructor(private client: HttpClient) {
        super(client);
    }
    initAuthorizationUrl() {
        this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.AUTHORIZATION;
        console.log("Authorization Url =>", this.authorizationUrl);
    }
    public authorizeRequest(targetUrl: string, targetId: number) {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.AUTHORIZATION
            + Defines.API_ENDPOINTS.AUTHORIZE;
            let authRequest: AuthorizationRequestModel = new AuthorizationRequestModel(targetUrl,targetId);

        return this.http.post<ApiResponse<AuthorizationRequestModel>>
            (apiUrl, new ApiRequest(authRequest, ""), this.httpOptions).toPromise();
    }

}
