import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Defines } from '../shared/static/defines';
import { BaseService } from '../shared/services/base-service';
import { ApiResponse } from '../shared/models/api-response';
import { ApiRequest } from '../shared/models/api-request';
import { Observable } from 'rxjs';
import { User } from '../shared/models/user-model';

@Injectable({
  providedIn: 'root'
})
export class SignUpService extends BaseService {


  constructor(private client: HttpClient) {
    super(client);
  }
  //Not Needed here
  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.SIGNUP;
    console.log("Authorization Url =>", this.authorizationUrl);
}
  public createUser(user: User): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.REGISTRATION + Defines.API_ENDPOINTS.SIGNUP;
    let obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(user, ""), this.httpOptions);
    return obs;
  }

  public checkUsernameExists(username: string): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.REGISTRATION + Defines.API_ENDPOINTS.checkUsernameExists;
    let obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(username, ""), this.httpOptions);
    return obs;
  }

  public checkEmailExists(email: string): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.REGISTRATION + Defines.API_ENDPOINTS.checkEmailExists;
    let obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(email, ""), this.httpOptions);
    return obs;
  }
}
