import { Component, OnInit } from '@angular/core';
import { RoleManagerService } from '../role-manager.service';
import { PrivilegeModel } from 'src/app/shared/models/privilege-model';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { RoleModel } from 'src/app/shared/models/role-model';
import { LookupModel } from 'src/app/shared/models/lookup-model';


@Component({
  selector: 'ndp-role-manager',
  templateUrl: './role-manager.component.html',
  styleUrls: ['./role-manager.component.css'],
  animations: [
    trigger('rowExpansionTrigger', [
      state('void', style({
        transform: 'translateX(-10%)',
        opacity: 0
      })),
      state('active', style({
        transform: 'translateX(0)',
        opacity: 1
      })),
      transition('* <=> *', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)'))
    ])
  ]
})
export class RoleManagerComponent implements OnInit {
  rolesList = new Array<RoleModel>();
  accessLevelList = new Array <LookupModel> ();
  selectedrole: string;
  cols: any[];
  editedRole: RoleModel;
  addRoleFlag = false;
  editRoleFlag = false;
  selectedCategories: any[] = [];
  allPrivileges = new Array<PrivilegeModel>();
  rowexpanded : any ;
  //targetPrivilege: PrivilegeModel[];
  constructor(private roleService: RoleManagerService) {
    //  this.rolesList.push({ label: 'Select Item', value: -1});
  }

  ngOnInit() {

    this.cols = [
      { field: 'id', header: 'ID' },
      { field: 'label', header: 'LABEL' },
      { field: 'descreption', header: 'DESCREPTION' },
      { field: 'isEditable', header: 'IS_EDITABLE' },
    ];


    this.roleService.getAllRoles().subscribe(roles => {
      console.log("role Received ==> ", roles.responsePayload);
      this.rolesList = roles.responsePayload;
      this.roleService.getAllPrivilege().subscribe(privilege => {
        //debugger;
        console.log("privilege ==> " + privilege.responsePayload);
        this.allPrivileges = privilege.responsePayload;

      });
    });

    this.roleService.getAllLevel().subscribe(levels => {
      console.log("role Received ==> ", levels.responsePayload);
      this.accessLevelList = levels.responsePayload;
     
    });
  

  }


editrole($event,tabel)
{
  //debugger;
  this.rowexpanded = false;
  //debugger;
  console.log("$event" , $event)
  this.editrole =$event;
  this.editedRole.label =$event.label;
  this.editedRole.descreption =$event.descreption;
  this.editedRole.isEditable =$event.isEditable;
  this.editedRole.privilegeList =$event.privilegeList;
  this.editedRole.id =$event.id;
  console.log("this.editrole" , this.editrole);
  this.addRoleFlag = true;
  //debugger;
      this.addRoleFlag= false;
}
  
  expandRow(role: RoleModel,expanded) {
    //debugger;
    this.rowexpanded = expanded;
    console.log("expanded  :" , expanded)
    this.editedRole =role;
    // Object.assign({}, role);

    this.editRoleFlag = !expanded ;
   
  }


  shwoAddRole() {
    this.addRoleFlag = true;
  }


  addrole($event){
    //debugger;
    this.addRoleFlag =false;
    this.rolesList.push($event);
  }

  cancelAdd(){
    this.addRoleFlag =true;
  }

  cancelEdit(){

  }
}
