import { Injectable } from '@angular/core';
import { SharedModule } from 'src/app/shared/shared.module';
import { CampaignModel } from '../shared/models/campaign-model';
import { B2bSenderNameModel } from '../shared/models/b2b_sender_name_model';
import { BaseService } from '../shared/services/base-service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { ApiRequest } from '../shared/models/api-request';
import { Defines } from '../shared/static/defines';
import { CampaignRequestModel } from '../shared/models/campaign-request-model';
import { TestSmsModel } from '../shared/models/test-sms-model';

@Injectable({
  providedIn: 'root'
})
export class CampaignService extends BaseService {

  constructor(private httpClient: HttpClient) {
    super(httpClient);
    console.log("Campaign Service" + this.backendUrl);
  }
  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS;
    console.log("Authorization Url =>", this.authorizationUrl);
}
  ngOnInit(): void {

  }

  public list(): Observable<ApiResponse<Array<CampaignModel>>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.LIST;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<CampaignModel>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);
    return obs;
  }

  public get(campaign: CampaignRequestModel): Observable<ApiResponse<CampaignModel>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.GET;
    const obs: Observable<any> = this.http.post<ApiResponse<CampaignModel>>
      (apiUrl, new ApiRequest(campaign, ''), this.httpOptions);
    return obs;
  }

  public create(campaign: CampaignRequestModel): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.CREATE;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<any>>>
      (apiUrl, new ApiRequest(campaign, ''), this.httpOptions);
    return obs;
  }

  public delete(campaign: CampaignRequestModel): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.DELETE;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<any>>>
      (apiUrl, new ApiRequest(campaign, ''), this.httpOptions);
    return obs;
  }

  public update(campaign: CampaignRequestModel): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.UPDATE;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<any>>>
      (apiUrl, new ApiRequest(campaign, ''), this.httpOptions);
    return obs;
  }

  public approve(campaign: CampaignRequestModel): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.approve;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<any>>>
      (apiUrl, new ApiRequest(campaign, ''), this.httpOptions);
    return obs;
  }

  public pause(campaign: CampaignRequestModel): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + Defines.API_ENDPOINTS.PAUSE;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<any>>>
      (apiUrl, new ApiRequest(campaign, ''), this.httpOptions);
    return obs;
  }

  public listSenders(): Observable<ApiResponse<Array<B2bSenderNameModel>>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.B2BSENDERNAME + Defines.API_ENDPOINTS.LIST;
    const obs: Observable<any> = this.http.post<ApiResponse<Array<any>>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);
    return obs;
  }

  public testSms(testSms: TestSmsModel): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.TEST_SMS;
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(testSms, ''), this.httpOptions);
    return obs;
  }

  public getCounts(): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + '/counts';
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(null, ''), this.httpOptions);
    return obs;
  }

  public getStats(n: number): Observable<ApiResponse<any>> {
    const apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.CAMPAIGNS + '/stats';
    const obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(n, ''), this.httpOptions);
    return obs;
  }
}
