import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseService } from '../shared/services/base-service';
import { ApiRequest } from '../shared/models/api-request';
import { Defines } from '../shared/static/defines';
import { LoginRequest } from '../shared/models/login-request';
import { Observable } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { AuthToken } from '../shared/models/auth-token';
import { map } from 'rxjs/operators';
import { ResetPasswordModel } from '../shared/models/reset-password-model';
import { FormGroup } from '@angular/forms';
@Injectable({
  providedIn: 'root'
})
export class LoginServiceService extends BaseService {
  constructor(private client: HttpClient) {
    super(client);
  }
  //Not needed here
  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.LOGIN;
    console.log("Authorization Url =>", this.authorizationUrl);
}
  // public sendLoginRequest(uesrname: string, password: string): Observable<AuthToken> {
  //   //debugger;
  //   let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.LOGIN;
  //   let obs: Observable<AuthToken> = this.http.post<AuthToken>(apiUrl, new LoginRequest(
  //     uesrname, password)
  //     , this.httpOptions).pipe(map((apiResponse: any) => new AuthToken(apiResponse.responsePayload.token, apiResponse.responsePayload.user)));
  //   //debugger;
  //   return obs;
  // }

  public sendLoginRequest(uesrname: string, password: string): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.LOGIN;
    let obs: Observable<ApiResponse<any>> = this.http.post<ApiResponse<any>>(apiUrl, new LoginRequest(
      uesrname, password)
      , this.httpOptions);
    return obs;
  }

  public sendLogoutRequest(): Observable<string> {
    //debugger;
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.logout;
    let obs: Observable<string> = this.http.post<string>(apiUrl, new ApiRequest(null, ""), this.httpOptions);
    //.pipe(map((apiResponse: any) => new AuthToken(apiResponse.responsePayload.token, apiResponse.responsePayload.user)));
    //debugger;
    return obs;
  }

  public forgot(email: string): Observable<ApiResponse<number>> {
    //debugger;
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.RESET + Defines.API_ENDPOINTS.FORGOT;
    let result: Observable<any> = this.http.post<string>(apiUrl, new ApiRequest(email, ""), this.httpOptions);
    //.pipe(map((apiResponse: any) => new AuthToken(apiResponse.responsePayload.token, apiResponse.responsePayload.user)));
    //debugger;
    return result;
  }

  public resetPassword(model: ResetPasswordModel): Observable<ApiResponse<number>> {
    //debugger;
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.RESET + Defines.API_ENDPOINTS.RESET;
    let result: Observable<any> = this.http.post<string>(apiUrl, new ApiRequest(model, ""), this.httpOptions);
    return result;
  }

  public resetExpiredPassword(model: ResetPasswordModel): Observable<ApiResponse<number>> {
    //debugger;
    let apiUrl = this.backendUrl + this.nonSecureApi + Defines.API_ENDPOINTS.RESET + Defines.API_ENDPOINTS.RESET_EXPIRED;
    let result: Observable<any> = this.http.post<string>(apiUrl, new ApiRequest(model, ""), this.httpOptions);
    return result;
  }

  public markFormGroupTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach(control => {
      control.markAsTouched();

      if (control.controls) {
        this.markFormGroupTouched(control);
      }
    });
  }
}
