import { Injectable } from '@angular/core';
import { BaseService } from '../shared/services/base-service';
import { HttpClient } from '@angular/common/http';
import { AppProfileModel } from '../shared/models/app-profile-model';
import { Observable } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { ApiRequest } from '../shared/models/api-request';
import { Defines } from '../shared/static/defines';

@Injectable({
  providedIn: 'root'
})
export class AppProfilesService extends BaseService {

  constructor(private httpClient: HttpClient) {
    super(httpClient);
    console.log("App Profile Service" + this.backendUrl);
  }

  initAuthorizationUrl() {
    this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.appprofile;
    console.log("Authorization Url =>", this.authorizationUrl);
  }

  ngOnInit(): void {

  }

  public list(): Observable<ApiResponse<Array<AppProfileModel>>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.appprofile + Defines.API_ENDPOINTS.LIST;
    let obs: Observable<any> = this.http.post<ApiResponse<Array<AppProfileModel>>>
      (apiUrl, new ApiRequest(null, ""), this.httpOptions);
    return obs;
  }

  public get(appProfileModel: AppProfileModel): Observable<ApiResponse<AppProfileModel>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.appprofile + Defines.API_ENDPOINTS.GET;
    let obs: Observable<any> = this.http.post<ApiResponse<AppProfileModel>>
      (apiUrl, new ApiRequest(appProfileModel, ""), this.httpOptions);
    return obs;
  }


  public create(appprofile: AppProfileModel): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.appprofile + Defines.API_ENDPOINTS.CREATE;
    let obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(appprofile, ""), this.httpOptions);
    return obs;
  }

  public update(appprofile: AppProfileModel): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.appprofile + Defines.API_ENDPOINTS.UPDATE;
    let obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(appprofile, ""), this.httpOptions);
    return obs;
  }
  public delete(appprofile: AppProfileModel): Observable<ApiResponse<any>> {
    let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.appprofile + Defines.API_ENDPOINTS.DELETE;
    let obs: Observable<any> = this.http.post<ApiResponse<any>>
      (apiUrl, new ApiRequest(appprofile, ""), this.httpOptions);
    return obs;
  }
}
