import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { CampaignModel } from 'src/app/shared/models/campaign-model';
import { Defines } from 'src/app/shared/static/defines';
import { CampaignService } from '../campaign.service';
import { CampaignStatisticsModel } from 'src/app/shared/models/campaign-stat-model';
import { formatDate } from '@angular/common';

@Component({
  selector: 'ndp-campaign-progress',
  templateUrl: './campaign-progress.component.html',
  styleUrls: ['./campaign-progress.component.css']
})
export class CampaignProgressComponent implements OnInit, OnDestroy {
  @Input() campaign: CampaignModel;
  campaignStats: CampaignStatisticsModel;
  private intervalId: any;
  private statsInterval: any;
  daysflag = true;
  constructor(private campaignService: CampaignService) { }

  ngOnInit() {
    // this.refreshData();
    this.intervalId = setInterval(() => { this.getDuration(this.campaign); }, 1000);
    this.refreshData();
    this.statsInterval = setInterval(() => { this.refreshData(); }, 60000);
  }

  ngOnDestroy() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
    if (this.statsInterval) {
      clearInterval(this.statsInterval);
    }
  }
  async refreshData() {
    if (this.getStatus(this.campaign) === this.displayStatus.RUNNING || this.displayStatus.FINISHED || this.displayStatus.PAUSED) {
      this.campaignService.getStats(this.campaign.campaignId).subscribe(template => {
        this.campaignStats = template.responsePayload;
      });
    }
  }
  getStatus(campaign: CampaignModel) {
    const startDate = new Date(campaign.startDate);
    const endDate = new Date(campaign.endDate);
    const currentDate = new Date();

    if (campaign.status === Defines.CAMPAIGN_STATUS.PAUSED) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.PAUSED;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.PENDING) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.PENDING;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.PROCESSING) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.PROCESSING_FILE;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.READY && currentDate < startDate) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.SCHEDULED;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.FAILED || campaign.status === Defines.CAMPAIGN_STATUS.SNAPSHOT_FAILED) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.FAILED;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.HANDLING || campaign.status === Defines.CAMPAIGN_STATUS.SMS_ENQUEUED
      || (campaign.status === Defines.CAMPAIGN_STATUS.READY && currentDate >= startDate)) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.RUNNING;
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.SUCCESS) {
      return Defines.CAMPAIGN_DISPLAY_STATUS.FINISHED;
    }
  }
  get displayStatus() {
    return Defines.CAMPAIGN_DISPLAY_STATUS;
  }

  getDuration(campaign: CampaignModel) {
    if (campaign.status === Defines.CAMPAIGN_STATUS.PENDING) {
      return 'Pending';
    }
    if (campaign.status === Defines.CAMPAIGN_STATUS.PAUSED) {
      return 'Paused';
    }
    const date1 = new Date();
    // const date2 = new Date(campaign.endDate);
    const date2 = new Date(campaign.startDate);
    if (date1 < date2) {
      const diffInMs: number = date2.getTime() - +date1.getTime();
      const diffInDays: number = diffInMs / 1000 / 60 / 60 / 24;
      const diffInHours: number = (diffInDays - Math.trunc(diffInDays)) * 24;
      if (Math.trunc(diffInDays) > 0) {
        this.daysflag = true;
        return 'In ' + Math.trunc(diffInDays) + ' Days';
      }
      this.daysflag = false;
      const remainingHours: number = (Math.floor((diffInMs / 1000) / 60 / 60)) % 24;
      const remainingMinutes: number = (Math.floor((diffInMs / 1000) / 60)) % 60;
      const remainingSeconds: number = (diffInMs / 1000) % 60;

      return (((Math.trunc(remainingHours) < 10) ? ('0' + Math.trunc(remainingHours)) : (Math.trunc(remainingHours))) + '    :    '
        + ((Math.trunc(remainingMinutes) < 10) ? ('0' + Math.trunc(remainingMinutes)) : (Math.trunc(remainingMinutes))) + '    :    '
        + ((Math.trunc(remainingSeconds) < 10) ? ('0' + Math.trunc(remainingSeconds)) : (Math.trunc(remainingSeconds))));
    }
  }

  getProgress() {
    if (this.campaignStats) {
      return (this.campaignStats.sent + this.campaignStats.delivered) >= 0 ? (this.campaignStats.sent + this.campaignStats.delivered) : 0;
    }
  }
  getTotal() {
    if (this.campaignStats) {
      return this.campaignStats.totalCount > 0 ? this.campaignStats.totalCount : 1;
    }
  }

  getPercentage() {
    /*const d = new Date();
    if (this.campaign.endDate && d <= this.campaign.endDate && d >= this.campaign.startDate) {
      return Math.round((+d.getTime() - (+this.campaign.startDate)) / (+this.campaign.endDate - (+this.campaign.startDate)) * 100);
    } else {
      return 0;
    }*/
    if (this.campaignStats) {
      return Math.round((this.getProgress() / this.getTotal()) * 100);
    }
  }
}
