import { Directive, ElementRef, Input, HostListener } from '@angular/core';

@Directive({
  selector: '[ndpActiveLink]'
})
export class ActiveLinkDirective {
  @Input() public activeClass: string;

  constructor(private htmlElement: ElementRef) { }

  @HostListener('click') onClick() {
    this.addStyle(this.activeClass);
  }



  private addStyle(style: string) {
    // this.htmlElement.nativeElement.style.backgroundColor = "red";
    let elementsWithClass : NodeListOf<Element> = document.querySelectorAll('.'+style);
    elementsWithClass.forEach(e => 
      e.classList.remove('active'));
    this.htmlElement.nativeElement.classList.add(style);

  }
}
