import { environment } from 'src/environments/environment'
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { FormGroup } from '@angular/forms';
import { Defines } from '../static/defines';
import { User } from '../models/user-model';
export abstract class BaseService {
   http: HttpClient;
   httpOptions = {
      headers: new HttpHeaders({
         'Content-Type': 'application/json',
         'Access-Control-Allow-Origin': '*',
         'Access-Control-Allow-Methods': 'POST',
         'Access-Control-Allow-Headers': 'Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With'
      })
   };
   constructor(httpClient: HttpClient) {
      this.http = httpClient;
      this.initAuthorizationUrl();
   }

   ngOnInit() {
      console.log("hwhwhwhhwhwhwhhwhwhwhhwhwhwhhwhwhwh");
      // this.initAuthorizationUrl();
   }
   backendUrl: string = environment.backendUrl;
   secureApi: string = environment.secureApiUrl;
   nonSecureApi: string = environment.nonSecureApiUrl;
   authorizationUrl: string = environment.backendContext; // to be completed by each service
   
   abstract initAuthorizationUrl();

   scrollToError(): void {
      // let invalidFields = [].slice.call(document.getElementsByClassName('ng-invalid'))
      // if (invalidFields && invalidFields.length > 0) {
      //    invalidFields[0].focus();
      // }

   }
   accessGranted(privId: number, accessLevel: number): boolean {
      let currentUser: User = JSON.parse(localStorage.getItem(Defines.SESSION_KEYS.userModel));
      debugger;
      console.log("privId in ==> ",privId,"accessLevel In => ",accessLevel);
      if (currentUser != null) {
         return currentUser.privilegeList.some((rolePriv):boolean => {
            console.log("Role Priv" ,rolePriv);
            if (rolePriv.privilegeId == privId && rolePriv.accessLevel == accessLevel) {
               console.log("Found Priv and Access Level");
               return true;
            } else {
               return false;
            }
         })
      } else {
         console.log("User not logged in");
         return false;
      }
      return false;
   }
   cacheSessionObject(key: string, object: any) {
      try {
         if (key && object) {
            sessionStorage.setItem(key, object);
         } else {
            throw "Invalid input used " + { "key": key, "object": object };
         }
      } catch (err) {
         console.log("failed to save object [" + key + "] to session!", err);
      }
   }

   popSessionObject(key: string): any {
      try {
         if (key) {
            let obj: any = sessionStorage.getItem(key);
            if (obj) {
               obj = JSON.parse(obj);
               sessionStorage.removeItem(key);
               return obj;
            } else {
               return null;
            }
         } else {
            throw "Null Key";
         }
      } catch (err) {
         console.log("failed to save object [" + key + "] to session!", err);
      }
      return null;
   }

   async scrollIfFormHasErrors(form: FormGroup): Promise<any> {
      try {
         await form.invalid;
         this.scrollToError();
      } catch (err) {
         console.log("Could not scroll to erroneous field ", err);
      }
   }

}
