import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { EntityModel } from 'src/app/shared/models/entity-model';


@Component({
  selector: 'ndp-entity-view',
  templateUrl: './entity-view.component.html',
  styleUrls: ['./entity-view.component.css']
})
export class EntityViewComponent implements OnInit {

  @Input("viewmodel")
  modelToview: EntityModel;
  @Output("action")
  actionTaken = new EventEmitter<number>();
  entityType: string;
  fakesender = "";
  groups: string = "";
  createdby = "";
  updatedby = "";
  creationDate: string;
  upddateDate: string;
  showFakeSender = false;
  shwoDlegatedGroup = false;

  constructor() { }

  ngOnInit() {
    this.entityType = this.modelToview.isInternal ? "Internal" : "external";

    this.modelToview.fakeSendrName.forEach(element => {
      this.showFakeSender =true;
      if (element.isDefault) {
        this.fakesender += element.fakeSenderName + " (*) , ";
      } else {
        this.fakesender += element.fakeSenderName + ","
      }
    });
    this.fakesender = this.fakesender.substring(0, this.fakesender.length - 1);


    this.modelToview.groupList.forEach(element => {
      this.groups += element.groupName + ","
      this.shwoDlegatedGroup = true;
    });
    this.groups = this.groups.substring(0, this.groups.length - 1);


    this.createdby = this.modelToview.creatorFName + " " + this.modelToview.creatorLName;
    this.updatedby = this.modelToview.updatedFName != null ? this.modelToview.updatedFName + " " + this.modelToview.updatedLName : "";
    let creationDate_Date = new Date(this.modelToview.creationDate);
    this.creationDate = creationDate_Date.getDate() + "-" + (creationDate_Date.getMonth() + 1) + "-" + creationDate_Date.getFullYear()
    let update_Date = new Date(this.modelToview.updatedDate);
    debugger;
    this.upddateDate = update_Date.getDate() + "-" + (update_Date.getMonth() + 1) + "-" + update_Date.getFullYear()

  }

  editEntity() {
    this.actionTaken.emit(1);
  }

  cancelEntity() {
    this.actionTaken.emit(2);
  }
}
