import { Component, OnInit } from '@angular/core';
import { RoleModel } from 'src/app/shared/models/role-model';
import { RoleManagerService } from '../role-manager.service';
import { NdpMessageService } from 'src/app/shared/services/ndp-message.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Defines } from 'src/app/shared/static/defines';
@Component({
  selector: 'ndp-role-list',
  templateUrl: './role-list.component.html',
  styleUrls: ['./role-list.component.css']
})
export class RoleListComponent implements OnInit {
  allRolesList = new Array<RoleModel>();
  filteredRolesList = null;
  roleId: number;
  inialized = false;

  dropDown: boolean;
  dropDownClick: number;
  dropDownId: number;

  deleteConfirm: boolean;
  deleteConfirmId: number;

  /* Pagination Filtering & Sorting*/
  p = 1;
  perPage = 5;
  /*FILTERING*/
  filter: string;
  /*SORTING*/
  key = 'startDate';
  reverse = true;
  /*=============================*/

    //Authorization Vars
    authorizationUrl: string;
    defines = Defines.API_CRUD;

  constructor(private roleService: RoleManagerService, private router: Router, private messageService: NdpMessageService) { }

  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }
  ngOnInit() {
    //this.filteredRolesList = this.allRolesList;
    this.authorizationUrl = this.roleService.authorizationUrl;
    this.roleService.getAllRoles().subscribe(roles => {
      this.allRolesList = roles.responsePayload;
      this.inialized = true;
    });
  }

  onCreateButtonClick() {
    this.router.navigate(['roles/create']);
  }
  onEditButtonClick(roleId: number) {
    console.log("Hi from onClick");
    this.router.navigate(['roles/edit', roleId]);
  }
  onDeleteButtonClick(roleId: number) {
    this.deleteConfirm = true;
    this.deleteConfirmId = roleId;
  }

  hideConfirmDelete() {
    this.deleteConfirm = false;
    this.deleteConfirmId = null;
  }

  delete(role: RoleModel) {
    this.deleteConfirm = false;
    if (role.userCount > 0) {
      this.messageService.addError('Delete Failed', 'Cannot delete a role that has assigned users');
      return;
    }
    this.roleService.deleteRole(role.id).subscribe(template => {
      if (template.status === 0) {
        this.allRolesList = this.allRolesList.filter(x => x.id !== role.id);
      }
    });
  }

  onShowDropDown(role: number) {
    this.dropDownClick = 0;
    this.dropDown = true;
    this.dropDownId = role;
  }
  onHideDropDown() {
    this.dropDownClick++;
    if (this.dropDown && this.dropDownClick > 1) {
      this.dropDown = false;
    }
  }

  resetPagination() {
    if (this.p > 1) {
      this.p = 1;
    }
  }

  setFilteredRolesList(filtered: RoleModel[]) {
    this.filteredRolesList = filtered;
  }
}
