import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { ApiRequest } from '../shared/models/api-request';
import { ApiResponse } from '../shared/models/api-response';
import { ReportRequestModel } from "../shared/models/ReportRequestModel";
import{ReportResponseModel}from "../shared/models/ReportResponseModel";
import { BaseService } from '../shared/services/base-service';
import { Defines } from '../shared/static/defines';
@Injectable({
    providedIn: 'root'
})
export class ReportsService extends BaseService {
    myHttpOptions = {
        headers: new HttpHeaders({
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'POST',
            'Access-Control-Allow-Headers': 'Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With',
            'Content-Type': 'text/csv',
            observe: 'response',
            responseType: 'text/csv'
        })
    };
    constructor(private client: HttpClient) {
        super(client);
    }
    initAuthorizationUrl() {
        this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.REPORTS;
        console.log("Authorization Url =>", this.authorizationUrl);
    }

    public generateReport(model: ReportRequestModel):Observable<ApiResponse<ReportResponseModel>> {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.REPORTS + Defines.API_ENDPOINTS.GENERATE_REPORT;
        console.log("REPORT params before generation is =>  ", model);
        debugger;
        const obs: Observable<any> = this.http.post<ApiResponse<ReportResponseModel>>
        (apiUrl, new ApiRequest(model, ""), this.httpOptions);
        return obs;
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {

            // TODO: send the error to remote logging infrastructure
            console.error(error); // log to console instead

            // TODO: better job of transforming error for user consumption
            console.log(`${operation} failed: ${error.message}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };

    }

}
