import { Injectable } from '@angular/core';
import { BaseService } from '../shared/services/base-service';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { ApiResponse } from '../shared/models/api-response';
import { Defines } from '../shared/static/defines';
import { ApiRequest } from '../shared/models/api-request';
import { catchError, map, tap } from 'rxjs/operators';
import { SmscAccountModel } from '../shared/models/smsc-account-model';

@Injectable({
    providedIn: 'root'
})
export class SmscService extends BaseService {
    constructor(private client: HttpClient) {
        super(client);
    }
    initAuthorizationUrl() {
        this.authorizationUrl += this.secureApi + Defines.API_ENDPOINTS.SMSC;
    }
    public create(model: SmscAccountModel): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.SMSC + Defines.API_CRUD.CREATE;
        console.log("model before create is => ", model);
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).pipe(
                tap(group => console.log('create smsc: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public update(model: SmscAccountModel): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.SMSC + Defines.API_CRUD.UPDATE;
        console.log("model before update is => ", model);
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(model, ""), this.httpOptions).pipe(
                tap(group => console.log('update smsc: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }


    public get(id: number): Observable<ApiResponse<SmscAccountModel>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.SMSC + Defines.API_CRUD.GET;
        let obs: Observable<any> = this.http.post<ApiResponse<SmscAccountModel>>
            (apiUrl, new ApiRequest(id, ""), this.httpOptions).pipe(
                tap(group => console.log('get smsc by id: ' + group)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public delete(id: number): Observable<ApiResponse<number>> {
        let apiUrl = this.backendUrl + this.secureApi +
            Defines.API_ENDPOINTS.SMSC + Defines.API_CRUD.DELETE;
        let obs: Observable<any> = this.http.post<ApiResponse<number>>
            (apiUrl, new ApiRequest(id, ""), this.httpOptions).pipe(
                tap(smsc => console.log('delete smsc by id: ' + smsc)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    public list(): Observable<ApiResponse<Array<SmscAccountModel>>> {
        let apiUrl = this.backendUrl + this.secureApi + Defines.API_ENDPOINTS.SMSC + Defines.API_CRUD.LIST;
        let obs: Observable<any> = this.http.post<ApiResponse<Array<SmscAccountModel>>>
            (apiUrl, new ApiRequest(null, ""), this.httpOptions).pipe(
                tap(list_ => console.log('get List smsc: ' + list_)),
                catchError(this.handleError<any>('Error'))
            );
        return obs;
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {

            // TODO: send the error to remote logging infrastructure
            console.error(error); // log to console instead

            // TODO: better job of transforming error for user consumption
            console.log(`${operation} failed: ${error.message}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };

    }

}
