import { Pipe, PipeTransform } from '@angular/core';
import { CampaignModel } from '../models/campaign-model';

@Pipe({
    name: 'dateFilter'
})
export class CampaignDateFilter implements PipeTransform {
    transform(campaigns: CampaignModel[], dateStart: Date, dateEnd: Date): any {
        let filtered = [];
        if (campaigns != null) {
            if (!dateStart && !dateEnd) {
                console.log("\n\n\n\n\n!dateStart && !dateEnd\n\n\n\n");
                return campaigns;
            } else if (dateStart && !dateEnd) {
                console.log("\n\n\n\n\ndateStart && !dateEnd\n\n\n\n");
                filtered = [];
                for (let i = 0; i < campaigns.length; i++) {
                    const c = campaigns[i];
                    if (c.startDate) {
                        const d = new Date(c.startDate);
                        if (d >= dateStart
                            //d.getDate() === dateStart.getDate()
                            //&& d.getMonth() === dateStart.getMonth()
                            //&& d.getFullYear() === dateStart.getFullYear()
                        ) {
                            filtered.push(c);
                        }
                    }
                }
                return filtered;
            } else if (!dateStart && dateEnd) {
                filtered = [];
                for (let i = 0; i < campaigns.length; i++) {
                    const c = campaigns[i];
                    console.log("\n\n\n\n\n\nEND DATE\n\n\n\n",c.endDate, c.name);
                    if (c.endDate) {
                        const d = new Date(c.endDate);
                        if (d <= dateEnd
                            // d.getDate() === dateEnd.getDate()
                            // && d.getMonth() === dateEnd.getMonth()
                            // && d.getFullYear() === dateEnd.getFullYear()
                        ) {
                            filtered.push(c);
                        }
                    }
                }
                return filtered;
            } else if (dateStart && dateEnd) {
                
                filtered = [];
                for (let i = 0; i < campaigns.length; i++) {
                    const c = campaigns[i];
                    if (c.startDate && c.endDate) {
                        const d1 = new Date(c.startDate);
                        const d2 = new Date(c.endDate);
                        if (d1 >= dateStart && d2 <= dateEnd) {
                            filtered.push(c);
                        }
                    }
                }
                return filtered;
            }

        }
    }
}
