
import { throwError as observableThrowError, Observable } from 'rxjs';

import { tap, catchError, map } from 'rxjs/operators';

import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse, HttpErrorResponse } from '@angular/common/http';


import { Defines } from '../static/defines';
import { Injectable } from '@angular/core';
import { NdpMessageService } from '../services/ndp-message.service';
import { ApiResponse } from '../models/api-response';
@Injectable()
export class MessageInterceptor implements HttpInterceptor {
    constructor(private messageService: NdpMessageService) { };
    intercept(
        req: HttpRequest<any>,
        next: HttpHandler
    ): Observable<HttpEvent<any>> {
        this.doPrework(req);

        let handleObs: Observable<HttpEvent<any>>;
        handleObs = next.handle(req).pipe(catchError((errorResponse, caught) => {
            console.log("Exception for Req=> ", req, "Exception => ", caught);
            this.handleErrorResponse(errorResponse, caught);
            return observableThrowError(errorResponse);
        }));
        return handleObs.pipe(tap(response => {
            if (response instanceof HttpResponse) {
                this.doPostwork(response);
            }
        }));

    }

    private doPrework(req: HttpRequest<any>) {
        console.log('Message Intercepter Prework');
    }

    private doPostwork(resp: HttpResponse<any>) {
        console.log("Response => ", resp);
        console.log("Is response ApiResponse =>" + (resp.body instanceof ApiResponse));
        if (resp.body) {
            let apiResponse: ApiResponse<any> = resp.body;
            // if (apiResponse.errors && apiResponse.errors.length > 0) {
            //     this.messageService.addErrors(apiResponse.errors);
            // }
            if (apiResponse.extraInfo && apiResponse.extraInfo.length > 0) {
                this.messageService.addInfos(apiResponse.extraInfo);
            } else if (apiResponse.warnings && apiResponse.warnings.length > 0) {
                this.messageService.addWarnings(apiResponse.warnings);
            } else if (apiResponse.status == Defines.API_RESPONSES.success) {
                // if (apiResponse.statusMessage && apiResponse.statusMessage.length > 0) {
                //     console.log("statusMessage",apiResponse.statusMessage);
                //     // this.messageService.addSuccess(apiResponse.statusMessage);
                // }
            } else {
                this.messageService.addError(apiResponse.status.toString(), apiResponse.statusMessage);
            }
        }
    }

    private handleErrorResponse(errorResponse: HttpErrorResponse, caught: Observable<HttpEvent<any>>) {
        console.log("errorResponse=>", errorResponse);
        if (errorResponse.status == 0) {
            this.messageService.addError(
                errorResponse.status.toString(),
                "Backend-System un-reachable!"
            );
        } else if (errorResponse.status == 401) {
            this.messageService.addError(
                errorResponse.status.toString(),
                "User is not Authorized"
            );
        } else {

            this.messageService.addError(
                errorResponse.status.toString(),
                errorResponse.statusText
            );
        }

        console.log('Message Intercepter ErrorResponse', errorResponse);
    }

}
