import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Pipe } from '@angular/core';
import { CalendarModule } from "primeng/calendar"
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LoginModule } from './login/login.module';
import { SmsTemplatesModule } from './sms-templates/sms-templates.module';
import { GroupModule } from './group/group.module';
import { GeneralModule } from './general/general.module';
import { NDPRoutingModule } from './ndprouting/ndprouting.module';
import { HomeModule } from './home/home.module';
import { CampaignsModule } from './campaigns/campaigns.module';
import { RolesModule } from './roles/roles.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { LoadingInterceptor } from './shared/interceptors/loading-interceptor';
import { AuthInterceptor } from './shared/interceptors/auth-interceptor';
import { MessageInterceptor } from './shared/interceptors/message-interceptor';
import { NdpspinnerModule } from './ndpspinner/ndpspinner.module';
import { DropdownModule } from 'primeng/dropdown';
import { AccountModule } from './account/account.module';
import { SharedModule } from './shared/shared.module';
import { EntityModule } from './entity/entity.module';
import { SignupModule } from './sign-up/signup/signup.module';
import { AppProfilesModule } from './app-profiles/app-profiles.module';
import { SmscModule } from './smsc/smsc.module';
import { ReportModule } from './reports/reports.module';
import { CookieService } from 'ngx-cookie-service';

export const httpInterceptors = [
  { provide: HTTP_INTERCEPTORS, useClass: LoadingInterceptor, multi: true },
  { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
  { provide: HTTP_INTERCEPTORS, useClass: MessageInterceptor, multi: true },
];
@NgModule({
  declarations: [
    AppComponent,
    // HabdComponentComponent
  ],
  imports: [
    BrowserModule,
    CalendarModule,
    BrowserAnimationsModule,
    GeneralModule,
    LoginModule,
    SmsTemplatesModule,
    GroupModule,
    NDPRoutingModule,
    HomeModule,
    CampaignsModule,
    RolesModule,
    HttpClientModule,
    NdpspinnerModule,
    DropdownModule,
    SharedModule,
    AccountModule,
    EntityModule,
    SignupModule,
    ReportModule,
    AppProfilesModule, SmscModule],
  providers: [httpInterceptors, CookieService],
  bootstrap: [AppComponent],
  exports: [GeneralModule]
})
export class AppModule { }
